import Base from 'modules/base'
import bind from 'delegate'
import { select, getCollection, closest } from 'lib/selectors'
import pageTitle from 'modules/pageTitle'
import OAB_API_MANAGER from './oab-api-manager'
import config from 'lib/config'
import notifier from 'modules/notifier'
import { isMobileScreen, isDesktopScreen } from 'lib/helpers'


const oabProvider = select('.js-oab-provider')
class OabServiceList extends Base {

  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.selectedAxis = this.master.oabDataObject.selectedAxis
    this.selectedDivision = this.master.oabDataObject.selectedDivision
    this.companyId = this.master.oabDataObject.companyId
    const defaultService = select('[data-default-service]')
    if (defaultService) {
      const dataCategory = defaultService.dataset.defaultService.split(',')
      this.master.oabDataObject.category = { categoryId: dataCategory[0], categoryName: dataCategory[1] }
    }

    this.selectedCategory = this.master.oabDataObject.category

    this.SELECTORS = {
      SECTION_TITLE: '.js-service-list-title',
      LIST: '.oab-service-list div[role=list]',
      ITEM_TEMPLATE: '.oab-service-list li.template',
      ITEMS: '.oab-service-list li:not(.template)',
      CATEGORY: '.js-category',
      PRICE_SCHEDULE: '.price-schedule',
      SERVICE_NAME: '.js-service-name',
      SERVICE_DESCRIPTION: '.js-description',
      OAB_CONTAINER: '.js-oab-container'
    }
    const oabContainer = closest(this.el, this.SELECTORS.OAB_CONTAINER)
    if (oabContainer && isMobileScreen()) oabContainer.classList.toggle('container', false)
    this.events = []
    this.clean()
    this.master.generateBanner(this.el)
    if(this.stepName != this.stepsArray[0])
        this.master.bnrButton()
    this.loadData()
  }

  clean = () => {
    this.LIST = select(this.SELECTORS.LIST)
    if (this.LIST) {
      // clean existing list if coming after clicking on back button
      while (this.LIST.firstChild) {
        this.LIST.removeChild(this.LIST.firstChild)
      }
    }
  }

	loadData(){
  if (oabProvider.innerText === 'SALESFORCE') {
      OAB_API_MANAGER.getServiceList({ division: this.selectedDivision}, [response => this.initCallback(response), response => this.master.displayErrorModalWindow(response), () => { this.master.destroyLoader() }])
  } else {
      OAB_API_MANAGER.getServiceList({ companyId: this.companyId, categoryId: this.selectedCategory ? this.selectedCategory.categoryId : 0 }, [response => this.initCallback(response), response => this.master.displayErrorModalWindow(response), () => { this.master.destroyLoader() }])
  }   
  }

  initCallback(response) {
    if (response.errors && response.errors.error) {
      this.master.displayErrorModalWindow()
      return
    }
    this.bindData(response)
    this.bindEvents()
    this.master.setHeaderHeight()
  }

  bindEvents() {
    this.events.push(bind(this.el, '.goto-button', 'click', this.nextStepCallback))
    this.events.push(bind(this.el, '.js-back-btn', 'click', this.master.backButton))
  }

  bindData(data) {
    this.serviceList = data.serviceList
    this.SECTION_TITLE = select(this.SELECTORS.SECTION_TITLE) || null
    this.LIST = select(this.SELECTORS.LIST) || null

    if (this.SECTION_TITLE) {
      this.SECTION_TITLE.innerHTML = ''
      if (oabProvider.innerText === 'SALESFORCE') {
        this.selectedAxis = `${window.config.oab.axisType[this.selectedAxis]}`
      }
      const title = this.master.createStepTitle((this.selectedCategory) ? this.selectedCategory.categoryName : this.selectedAxis, 'lbl_Axiscategory')
      this.SECTION_TITLE.insertBefore(title, this.SECTION_TITLE.childNodes[0])
      title.focus()

      if(window.config.country == "MX" && isDesktopScreen()) {
        title.style.fontSize = "25px"
      }
    }
    pageTitle((this.selectedCategory && this.selectedCategory.categoryName.toLowerCase() !== this.selectedAxis.toLowerCase()) ? `${this.selectedCategory.categoryName} - ${this.selectedAxis}` : this.selectedAxis, '', ' - ')

    if (this.LIST) {
      let imageServices = data.serviceList.filter(item => item.media !== null)
      let noImageServices = data.serviceList.filter(item => item.media === null)
      const newItemImages = document.createElement('div')
      newItemImages.setAttribute('role', 'presentation')
      noImageServices.length > 0 ? newItemImages.classList.add('has-image') : 0
      imageServices.forEach((element, index) => {
        this.createImage(newItemImages, element, index, true)
      })

      const newItemNoImages = document.createElement('div')
      newItemNoImages.setAttribute('role', 'presentation')
      imageServices.length > 0 ? newItemNoImages.classList.add('no-image') : ''
      noImageServices.forEach((element, index) => {
        this.createImage(newItemNoImages, element, index, false)
      })
    }
    this.servicesList = getCollection('.service-item')
  }

  createImage(images, element, index, hasImage) {
    const listItem = document.createElement('div')
        listItem.setAttribute('role', 'listitem')
        listItem.setAttribute('data-service-id', element.serviceId)
        images.appendChild(listItem)
        listItem.classList.add('service-item')
        listItem.classList.add('has-text-left')
        let idAttribute = hasImage ? `oab_service_${index}` : `oab_service_0${index}`
        let imgAlt = config.oab.imageAltTexts[element.extra.imagealt] ?  config.oab.imageAltTexts[element.extra.imagealt] : ''
        let imgTemplate = index ===0 ? `<img role="presentation" aria-hidden="true" width="282" height="375" src = "${element.media ? element.media.url: null}" alt="${imgAlt}"/>` : `<img role="presentation" aria-hidden="true" class="is-lazy" width="282" height="375" data-src = "${element.media ? element.media.url: null}" alt="${imgAlt}"/>`
        listItem.innerHTML = hasImage ? imgTemplate + this.getTemplate(idAttribute, element.media ? element.media.url: null, element.extra.imagealt)
        : this.getTemplate(idAttribute, element.media ? element.media.url: null, element.extra.imagealt)

        const category = select(this.SELECTORS.CATEGORY, listItem) || null
        const serviceName = select(this.SELECTORS.SERVICE_NAME, listItem) || null
        const priceSchedule = select(this.SELECTORS.PRICE_SCHEDULE, listItem) || null
        const serviceDescription = select(this.SELECTORS.SERVICE_DESCRIPTION, listItem) || null


        if (serviceName) {
          serviceName.innerHTML = element.title
          serviceName.id = element.serviceId
          serviceName.setAttribute('data-test', 'lblServiceName')
        }
        if (serviceDescription && element.description) {
          serviceDescription.innerHTML = this.wrapDescription(element.description)
        }
        else {
          serviceDescription.parentElement.removeChild(serviceDescription)
        }

        if (element.category && category) category.innerHTML = element.category
        else {
          category.parentElement.removeChild(category)
        }

        if ((element.price || element.duration) && priceSchedule) {
          const durationUnit = (['JP', 'TW', 'HK'].includes(window.config.country) ? '' : ' ') + window.config.oab.oabDurationUnit

          priceSchedule.innerHTML = `${element.duration ? `${element.duration.duration}${durationUnit}` : ''}
          ${element.price && element.price.value > 0 && element.price.formattedValue ? ` - ${element.price.formattedValue}` : ''}`
        }

        else {
          listItem.removeChild(priceSchedule)
        }

        this.LIST.appendChild(images)
  }
  // Stepper callback
  nextStepCallback = (evt) => {
  if (evt.detail <= 1) {
    if (this.stepName != this.stepsArray[0])
      this.master.setWhiteBanner()
    this.master.oabDataObject.service = this.serviceList.filter(service=>service.serviceId === parseInt(evt.delegateTarget.parentElement.getAttribute('data-service-id')))[0] // store selected service in the global data object to use in next steps
    const serviceName = select(this.SELECTORS.SERVICE_NAME, evt.delegateTarget.offsetParent) || null
    this.master.analyticsHandler('oab-when-user-select-service', {
      eventAction: this.master.oabDataObject.selectedAxis,
      eventLabel: this.master.oabDataObject.service.title
    })
    // check if questions are linked to the service or not
    if (this.master.oabDataObject.service.questionSetId !== 0) {
      const collectionCategoryParams = {
        questionSetId: this.master.oabDataObject.service.questionSetId,
        companyId: this.companyId
      }
      // call categories and collection webservice to check if question is a comment or a category & collection
      OAB_API_MANAGER.getCategoryCollectionList(collectionCategoryParams)
        .then((data) => {
          this.master.oabDataObject.questionListData = data
          this.questionList = data.questionList
          if (this.questionList) {
            this.questionList.forEach((question) => {
              this.master.oabDataObject.interests = question.questionName.toLowerCase() === 'customer_interests'
              // if question is collection and category then redirect to collection and categories step
              if (question.answerList !== null && question.questionName.toLowerCase() !== 'comment' && question.questionName.toLowerCase() !== 'customer_interests') {
                this.master.oabDataObject.questionCommentDate = question
                this.master.nextStep('collection-category')
              } else if (question.questionName.toLowerCase() === 'comment') {
                // else if question is a comment then don't redirect to collection and categories step
                this.chooseRedirectionStep()
              }
            })
          } else if (data.errors) { // if an error occurs while calling collection and categories webservice then show the error notifier
            notifier.show({
              name: window.config.locales.notifier.request.label,
              message: window.config.locales.notifier.request.message,
              close: window.config.locales.notifier.request.close,
            })
            this.chooseRedirectionStep() // bypass the collection and category step to avoid blocking the user
          }
        })
    } else {
      this.chooseRedirectionStep()
    }
  }
}

  chooseRedirectionStep() {
    if (this.master.oabDataObject.service.description && this.master.oabDataObject.service.description.length > 125) { // if the selected service contains a description and description has more than 125 characters then go to description step
      this.master.nextStep('description')
    } else {
      if (this.master.oabDataObject.service.extra.mode != null && this.master.oabDataObject.service.extra.mode.toLowerCase() === 'video') {
        this.master.nextStep('date-selection') // go to date selection if user has selected virtual service
        window.sessionStorage.setItem('selectedSpanAriaLabel', '')
      }
      else {
        this.master.nextStep('boutique-selection') // else go directly to boutique selection step
      }
    }
  }

  // gets called by the master to get back button analytics
  getBackStepAnalytics() {
    return {
      eventCategory: 'service list',
      eventLabel: 'back',
      eventAction: (this.selectedCategory) ? this.selectedCategory.categoryName : this.selectedAxis
    }
  }

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
    delete this.master.oabDataObject.service
  }

  wrapDescription = (description) => {
    return (description.length > 125) ? `${description.substring(0, description.substring(0, 120).lastIndexOf(' '))}...` : description
  }

  getTemplate = (idAttribute) => `<div class="service-text">
            <p class="heading is-8 js-category" data-test="lbl_Axiscategory"></p>
            <h2 id = "${idAttribute}">
                  <span role="presentation" class="heading is-5 js-service-name" data-test="lblServiceName"></span>
            <span role="presentation" class="heading is-7 price-schedule"></span>
            </h2>
            <p class="description js-description" data-test="lblDescription"></p>
            
        </div>
        <button class="goto-button" type="button" aria-describedby="${idAttribute}">${window.config.oab.servcieSelection.bookCtaText}</button>`

}

export default OabServiceList
