import * as xhr from 'modules/fetch'

const OAB_API_MANAGER = {
  /**
   * @param {Object} requestData - data to send to WS
   * @param {Array} callbacks [successCallback, rejectCallback, finallyCallback] : array containing promise callbacks
   */
  getDateAndTime: (requestData, callbacks = [null, null, null]) => {
    xhr.get(window.config.webservices.oab.dateSelection, { data: requestData }).then(callbacks[0], callbacks[1], callbacks[2])
  },
  getEventDateAndTime : (requestData, callbacks = [null, null, null]) => {
    xhr.get(window.config.webservices.oab.eventDateSelection, { data: requestData }).then(callbacks[0], callbacks[1], callbacks[2])
  },

  /**
   * @see getOabDateAndTime
   */
  getAvailableBoutiques: (requestData, callbacks = [null, null, null]) => {
    xhr.get(window.config.webservices.oab.boutiqueList, { data: requestData }).then(callbacks[0], callbacks[1], callbacks[2])
  },

  getAvailableBoutiquesByService: (requestData, callbacks = [null, null, null]) => {
    xhr.get(window.config.webservices.oab.boutiqueListByService, { data: requestData }).then(callbacks[0], callbacks[1], callbacks[2])
  },

  getAvailableBoutiquesByEvent: (requestData, callbacks = [null, null, null]) => {
    xhr.get(window.config.webservices.oab.boutiqueListByEvent, { data: requestData }).then(callbacks[0], callbacks[1], callbacks[2])
  },

  /**
   * @see getOabDateAndTime
   */
  postDatetimeFormData: (requestData, callbacks = [null, null, null]) => {
    // xhr.post('oab/date', requestData).then(callbacks[0], callbacks[1], callbacks[2])
    new Promise((resolve) => {
      resolve({ data: ['11:00 AM', '13:00 pm', '16:00 pm', '17:00 AM', '14:30 AM'] })
      // reject(false)
    })
      .then(callbacks[0], callbacks[1], callbacks[2])
  },

  /**
   * @see getOabDateAndTime
   */
  getServiceList: (requestData, callbacks = [null, null, null]) => {
    xhr.get(window.config.webservices.oab.serviceList, { data: requestData }).then(callbacks[0], callbacks[1], callbacks[2])
  },

  /**
   * @see getOabDateAndTime
   */
  getCategoryList: (requestData, callbacks = [null, null, null]) => {
    xhr.get(window.config.webservices.oab.categoryList, { data: requestData }).then(callbacks[0], callbacks[1], callbacks[2])
  },
  /**
   * @see getOabCategoryAndCollection
   */

  getCategoryCollectionList: (requestData) => xhr.get(window.config.webservices.oab.categoryCollectionList, { data: requestData }),

  addAppointmentBooking: (requestData) => xhr.post(window.config.webservices.oab.postAppointmentBooking, { data: requestData }),

  addEventAppointmentBooking: (requestData) => xhr.post(window.config.webservices.oab.postEventAppointmentBooking, { data: requestData }),

  /**
   * @see postOabBookingConfirmation
   */
  postAppointmentBookingConfirmation: (requestData) => xhr.post(window.config.webservices.oab.confirmBooking, { data: requestData }),


  postAppointmentEventBookingConfirmation: (requestData) => xhr.post(window.config.webservices.oab.eventConfirmBooking, { data: requestData }),

  postAppointmentStartCheckout: (requestData) => xhr.post(window.config.webservices.oab.confirmOABEventBooking, { data: requestData }),

  /**
   * @see postOabBookingConfirmation
   */
  postAppointmentGuestBookingConfirmation: (requestData) => xhr.post(window.config.webservices.oab.confirmGuestBooking, { data: requestData }),


  /**
     * @see postOabEventBookingConfirmation
     */
    postAppointmentGuestEventBookingConfirmation: (requestData) => xhr.post(window.config.webservices.oab.confirmGuestEventBooking, { data: requestData }),

  /**
   * @see deleteOabBooking
   */
  deleteAppointmentBooking: (requestData) => xhr.post(window.config.webservices.oab.cancelBooking, { data: requestData })

}

export default OAB_API_MANAGER
