import Base from 'modules/base'
import bind from 'delegate'
import { select, closest } from 'lib/selectors'
import pageTitle from 'modules/pageTitle'
import config from 'lib/config'
import { isMobileScreen } from 'lib/helpers'

class OabCategory extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
	  this.SELECTORS = {
      SECTION_TITLE: '.js-category-selection-title',
      TEMPLATE: '.category-item.template',
      LIST: '.oab-category-list ul',
      OAB_CONTAINER: '.js-oab-container'
    }
    this.selectedAxis = this.master.oabDataObject.selectedAxis
    this.divisionAxis = this.master.oabDataObject.selectedDivision
    const oabContainer = closest(this.el, this.SELECTORS.OAB_CONTAINER)
    if (oabContainer && isMobileScreen()) oabContainer.classList.toggle('container', false)
    this.events = []
    this.setupTitle()
    if (!select('[data-default-list]')) {
      this.clean()
      this.loadData()
    }
    else {
      this.bindEvents()
      this.master.setHeaderHeight()
    }
    this.master.generateBanner(this.el)
    if(this.stepName != this.stepsArray[0])
        this.master.bnrButton()
  }

  clean = () => {
    this.LIST = select(this.SELECTORS.LIST)
    if (this.LIST) {
      // clean existing list if coming after clicking on back button
      while (this.LIST.firstChild) {
        this.LIST.removeChild(this.LIST.firstChild)
      }
    }
  }
  
  // gets called by the master to get back button analytics
  getBackStepAnalytics() {
    return {
      eventCategory: 'category selection',
      eventLabel: 'back',
      eventAction: this.selectedAxis
    }
  }

  loadData() {
    const categoryResponse = this.master.oabDataObject.categoryListObject
    this.initCallback(categoryResponse)
  }

  initCallback = (response) => {
    if (response.errors && response.errors.error) {
      this.master.displayErrorModalWindow()
      return
    }
    this.bindData(response)
    this.bindEvents()
    this.master.setHeaderHeight()
  }

  bindData = (response) => {
    this.master.oabDataObject.companyId = response.companyId

    response.categoryList.forEach(element => {
      const newItem = document.createElement('li')
      newItem.classList.add('category-item')
      newItem.classList.add('is-fullwidth')
      newItem.innerHTML = this.getTemplate()

      const spanCompanyName = select('span', newItem)
      if (spanCompanyName) {
        spanCompanyName.id = element.categoryId
        spanCompanyName.innerHTML = element.name
      }

      this.LIST.appendChild(newItem)
    })
  }

  bindEvents = () => {
    const obj = bind(this.el, '.category-item button', 'click', this.nextStepCallback)
    this.events.push(obj)
    this.events.push(bind(this.el, '.js-back-btn', 'click', this.master.backButton))
  }

  setupTitle = () => {
    this.SECTION_TITLE = select(this.SELECTORS.SECTION_TITLE)

    pageTitle(this.selectedAxis, '', ' - ')

    this.SECTION_TITLE.innerHTML = ''
    let selectedAxis
    selectedAxis = this.master.oabDataObject.selectedAxis.indexOf('watches-jewellery') !== -1 ? 'watches' : this.master.oabDataObject.selectedAxis
    const title = this.master.createStepTitle(config.oab.axisType[selectedAxis], 'lblCategorySelection')
    this.SECTION_TITLE.insertBefore(title, this.SECTION_TITLE.childNodes[0])
    title.focus()
  }

  nextStepCallback = (event) => {
    if(this.stepName != this.stepsArray[0])
        this.master.setWhiteBanner()
    if (event) {
      const spanCompanyName = select('span', event.delegateTarget) || null
      if (spanCompanyName) {
        this.master.oabDataObject.category = {
          categoryId: spanCompanyName.id,
          categoryName: spanCompanyName.innerHTML
        }
        this.master.analyticsHandler('oab-when-user-select-category', {
          eventAction: this.selectedAxis,
          eventLabel: (spanCompanyName) ? spanCompanyName.innerHTML : '',
        })
      }
    }
    this.master.nextStep('service-list')
  }

  getTemplate = () => {
    return `
        <button type="button" class="button">
            <span class="is-7"></span>
            <div class="goto-icon">
              <svg class="icon is-next" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="${config.paths.icons}icons.svg${config.targetVersion}#next"></use>
              </svg>
            </div>
        </button>`
  }

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
    delete this.master.oabDataObject.category
  }

}

export default OabCategory
