import Base from 'modules/base'
import bind from 'delegate'
import OAB_API_MANAGER from './oab-api-manager'
import { select, selectAll , closest, getCollection} from 'lib/selectors'
import { focusElement, isMobileScreen, initGoogleMap } from 'lib/helpers'
import pageTitle from 'modules/pageTitle'

class OabBoutiqueSelection extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.oabData = this.master.oabDataObject
    this.stepsArray = this.oabData.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.selectedAxis = this.oabData.selectedAxis
	this.selectedDivision = this.oabData.selectedDivision
    this.companyId = this.oabData.companyId
    this.events = []
    this.boutiqueSelectionIndex = 0
    this.SELECTORS = {
      SECTION_TITLE: '.js-boutique-selection-title',
      LIST: '.oab-boutique-list ul',
      ITEM_TEMPLATE: '.oab-boutique-list li.template',
      ITEMS: '.oab-boutique-list li',
      ITEMS_LABEL: '.oab-boutique-list label',
      ITEMS_RADIO: '.oab-boutique-list input',
      BOUTIQUE_NAME: '.js-boutique-name',
      BOUTIQUE_ADDRESS: '.js-boutique-address',
      FORM_CTA: '.oab-boutique-list .submitButton',
      SUBMIT_FORM_CTA: '.boutique-map .submitButton',
      OAB_CONTAINER: '.js-oab-container',
      BOUTIQUE_TITLE: '.oab-boutique-title',
      BOUTIQUE_TABS: '.boutique-tabs',
      LIST_TAB: '.list-tab',
      MAP_TAB: '.map-tab',
      BOUTIQUE_INFO: '.js-boutiqueInfo',
      BUTTON_NAVIGATE_TOP: '.button-navigate-to-top',
      BOUTIQUE_LIST: '.oab-boutique-list',
      BOUTIQUE_MAP: '.boutique-map',
      MAP_TOP: '#skip_storemap_top',
      MAP_BOTTOM: '#skip_storemap_bottom'
    }
    const oabContainer = closest(this.el, this.SELECTORS.OAB_CONTAINER)
    if (oabContainer && isMobileScreen()) oabContainer.classList.toggle('container', true)
    this.loadData()
  }

  // gets called by the master to get back button analytics
  getBackStepAnalytics() {
    return {
      eventCategory: 'boutique selection',
      eventLabel: 'back',
      eventAction: ''
    }
  }

  /* ---------- LOADING ---------- */

  loadData() {
	const oabProvider = select('.js-oab-provider')
    this.callGoogleMaps()
    if(this.master.oabDataObject.service.associatedEvents) {
      OAB_API_MANAGER.getAvailableBoutiquesByEvent({
        companyId: this.master.oabDataObject.companyId,
        eventName: this.master.oabDataObject.service.title,
        eventGroupName:  this.master.oabDataObject.service.eventGroupName
      }, [response => this.init(response), response => this.master.displayErrorModalWindow(), () => this.master.destroyLoader()])
    } else {
		if(oabProvider.innerText === "SALESFORCE"){
			OAB_API_MANAGER.getAvailableBoutiquesByService({
				division: this.selectedDivision,
				serviceType: this.master.oabDataObject.service.salesforceId
			}, [response => this.init(response), response => this.master.displayErrorModalWindow(), () => this.master.destroyLoader()])
			}else{
			OAB_API_MANAGER.getAvailableBoutiquesByService({
				companyId: this.companyId,
				serviceId: this.master.oabDataObject.service.associatedEvents ? this.master.oabDataObject.service.eventGroupId : this.master.oabDataObject.service.serviceId
				}, [response => this.init(response), response => this.master.displayErrorModalWindow(), () => this.master.destroyLoader()])
				}
			}
  }

  init(response) {
    if (response.errors && response.errors.error) {
      this.master.displayErrorModalWindow()
      return
    }
    this.SECTION_TITLE = select(this.SELECTORS.SECTION_TITLE)
    this.bindData(response)
    this.bindEvents()
    this.loadSelectedBoutiqueMap()
    this.master.setHeaderHeight()
  }

  /* ---------- BINDING ---------- */

  bindData(data) {
    this.LIST = select(this.SELECTORS.LIST) || null
    this.boutiqueTitle = select(this.SELECTORS.BOUTIQUE_TITLE) || null
    this.boutiqueTabs = select(this.SELECTORS.BOUTIQUE_TABS) || null
    this.ITEM_TEMPLATE = select(this.SELECTORS.ITEM_TEMPLATE)
    this.boutiqueInfo = select(this.SELECTORS.BOUTIQUE_INFO) || null
    this.boutiqueList = select(this.SELECTORS.BOUTIQUE_LIST)
    this.boutiqueMap = select(this.SELECTORS.BOUTIQUE_MAP)
    this.mapTab = select(this.SELECTORS.MAP_TAB) || null
    this.listTab = select(this.SELECTORS.LIST_TAB) || null
    this.selectedListItem = null
    if (this.SECTION_TITLE) {
      const title = this.master.createStepTitle(window.config.oab.boutiqueSelection.title, 'lblBoutiqueSelection')
      pageTitle(`${window.config.oab.boutiqueSelection.title} - ${window.config.oab.title}`, '', ' | ')
      this.SECTION_TITLE.innerHTML = ''
      this.SECTION_TITLE.appendChild(title)
      focusElement(this.SECTION_TITLE.firstChild)
    }
    if (this.LIST && this.ITEM_TEMPLATE) {
      this.data = data.boutiques
      // clean existing list if coming after clicking on back button
      this.boutiqueTabs ? this.boutiqueTabs.remove() : ''
      this.boutiqueInfo ? this.boutiqueInfo.remove() : ''
      while (this.LIST.firstChild) {
        this.LIST.removeChild(this.LIST.firstChild)
      }
      data.boutiques.forEach((element, idx) => {
        const newItem = this.ITEM_TEMPLATE.cloneNode(true)
        newItem.classList.remove('template')
        newItem.removeAttribute('hidden')
        newItem.setAttribute('data-lat', element.address.latitude)
        newItem.setAttribute('data-long', element.address.longitude)
        newItem.setAttribute('data-index', idx)

        const radio = select('input', newItem)
        const label = select('label', newItem)

        if (radio) {
          radio.setAttribute('id', `boutique-${idx}`)
          radio.value = idx
        }
        if (label) label.setAttribute('for', `boutique-${idx}`)

        if (this.oabData.selectedBoutique && this.oabData.comingFromBack) {
          if (this.oabData.selectedBoutique.boutiqueIndex === idx) {
            newItem.classList.toggle('selected', true)
            if (radio) radio.setAttribute('checked', '')
            this.selectedBoutique = element
            this.selectedBoutique.boutiqueIndex = idx
          }
        } else if (idx === 0) {
          newItem.classList.toggle('selected', true)
          if (radio) radio.setAttribute('checked', '')
          this.selectedBoutique = element
          this.selectedBoutique.boutiqueIndex = idx
        }

        const name = select(this.SELECTORS.BOUTIQUE_NAME, newItem) || null
        const address = select(this.SELECTORS.BOUTIQUE_ADDRESS, newItem) || null


        if (name) name.innerHTML = element.name
        if (address) {

          if(element.address.postcode != null && element.address.address4 != null){address.innerHTML = `${element.address.address1 ? element.address.address1 : ''} -

          ${element.address.postcode ? element.address.postcode : ''}

          ${element.address.address4 ? element.address.address4 : ''}`}else{

            address.innerHTML = `${element.address.address1 ? element.address.address1 : ''}`

          }

          this.address = address.innerHTML

        }
        this.LIST.appendChild(newItem)
      })
      if (isMobileScreen()) {
        this.createMobileTabs()
        this.showTabContent()
      }

      if(this.boutiqueSelectionIndex == 0) {
        this.selectedBoutiqueItem = select(`${this.SELECTORS.LIST} .selected`)
          if (this.selectedBoutiqueItem) {
            let scrollPos = 0
            if(!isMobileScreen()) {
              scrollPos = this.selectedBoutiqueItem.offsetTop - this.selectedBoutiqueItem.offsetHeight
            }
            if(isMobileScreen()) {
              let boutiqueTabs = select('.boutique-tabs')
              scrollPos = this.oabData.comingFromBack == false ? this.selectedBoutiqueItem.offsetHeight - this.selectedBoutiqueItem.offsetTop
                              : this.selectedBoutiqueItem.offsetTop - 2 * (this.selectedBoutiqueItem.offsetHeight) - boutiqueTabs.offsetHeight
            }
            this.LIST.scrollTo({
              top: scrollPos
            })
          }
          this.boutiqueSelectionIndex++
      }

    }
  }

  callGoogleMaps() {
    initGoogleMap()
  }

  createMobileTabs() {
    this.template = `<ul class = "boutique-tabs">
    <li> <button class = "tab-item list-tab" type="button">${config.oab.boutiqueSelection.listTab}</button></li>
    <li><button class = "tab-item map-tab" type="button">${config.oab.boutiqueSelection.mapTab}</button</li>
    </ul>`
    this.boutiqueTitle.insertAdjacentHTML("afterend", this.template)
    let boutiqueMap = select('.boutique-map')
    boutiqueMap.setAttribute('hidden', '')
    this.listTab ? this.listTab.classList.add('selected') : ''
    select(this.SELECTORS.SECTION_TITLE).classList.remove('element-centered')
  }

  loadSelectedBoutiqueMap() {
    let map
    let style = [{
        featureType: "all",
        elementType: "all",
        stylers: [{
          saturation: -100
        }],
      },

      {
        elementType: "labels.icon",
        stylers: [{
          visibility: "off",
        }, ],
      },
    ]
    let latitude = this.selectedBoutique.address.latitude
    let longitude = this.selectedBoutique.address.longitude
    let latlngbounds = new google.maps.LatLngBounds();
    let mapOptions = {
      zoom: 17,
      center: new google.maps.LatLng(latitude, longitude),
      mapTypeControlOptions: {
        mapTypeIds: [google.maps.MapTypeId.ROADMAP, 'grey']
      },
      disableDefaultUI: true,
      zoomControl: true,
      gestureHandling: "greedy",
    }

    map = new google.maps.Map(document.getElementById("oab-boutique-map"), mapOptions)
    var mapType = new google.maps.StyledMapType(style, {
      name: "Grayscale"
    })
    map.mapTypes.set("grey", mapType)
    map.setMapTypeId("grey")
    var marker_image = `${config.theme}/assets/media/pin-black.png`
    for (i = 0; i < this.data.length; i++) {
      var pinIcon = (Number(this.data[i].address.latitude) === this.selectedBoutique.address.latitude && Number(this.data[i].address.longitude) === this.selectedBoutique.address.longitude) ? new google.maps.MarkerImage(marker_image, null, null, null, new google.maps.Size(30, 30)) : new google.maps.MarkerImage(marker_image, null, null, null, new google.maps.Size(22, 22))
      const marker = new google.maps.Marker({
        map: map,
        position: new google.maps.LatLng(this.data[i].address.latitude, this.data[i].address.longitude),
        icon: pinIcon,
      });
      latlngbounds.extend(marker.position);
      const that = this
      let allMarkers = []
      allMarkers.push(marker)
      marker.addListener("click", function() {
        that.selectMarkerBoutique(marker, allMarkers);
      })
    }
  }

  selectMarkerBoutique(marker, allMarkers) {
    const boutiqueList = selectAll(this.SELECTORS.ITEMS)
    boutiqueList.forEach((item, index) => {
      if (Number(item.dataset.lat) === marker.position.lat() && Number(item.dataset.long) === marker.position.lng()) {
        item.classList.toggle('selected', true)
        select('input', item).setAttribute('checked', '')
        this.selectedListItem = item
        this.selectedBoutique = this.data[index]
        this.selectedBoutique.boutiqueIndex = index
      } else {
        item.classList.toggle('selected', false)
        select('input', item).removeAttribute('checked')
      }
    })
    const scrollPos = this.selectedListItem.offsetTop - this.selectedListItem.offsetHeight
    this.LIST.scrollTo({
      top: scrollPos,
      behavior: 'smooth'
    })
    this.boutiqueInfo = select(this.SELECTORS.BOUTIQUE_INFO)
    this.boutiqueInfo ? this.boutiqueInfo.remove() : ''
    let boutiqueInfoButton = select('.map-button-navigate-to-top')
    boutiqueInfoButton ? boutiqueInfoButton.remove() : ''
    if (isMobileScreen())
      this.createBoutiqueInfoBox()
    this.loadSelectedBoutiqueMap()
  }

  bindEvents() {
    this.events.push(bind(this.el, this.SELECTORS.ITEMS_RADIO, 'focusin', (evt) => this.focusInCallback(evt)))
    this.events.push(bind(this.el, this.SELECTORS.ITEMS, 'click', (evt) => this.focusInCallback(evt)))
    this.events.push(bind(this.el, this.SELECTORS.ITEMS_RADIO, 'focusout', (evt) => this.focusOutCallback(evt)))
    this.events.push(bind(this.el, this.SELECTORS.FORM_CTA, 'click', (evt) => this.submitCallback(evt)))
    this.events.push(bind(this.el, this.SELECTORS.SUBMIT_FORM_CTA, 'click', (evt) => this.submitCallback(evt)))
    this.events.push(bind(this.el, this.SELECTORS.LIST_TAB, 'click', (evt) => this.showTabContent(evt)))
    this.events.push(bind(this.el, this.SELECTORS.MAP_TAB, 'click', (evt) => this.showTabContent(evt)))
    this.events.push(bind(this.el, this.SELECTORS.BUTTON_NAVIGATE_TOP, 'click', (evt) => this.focusToActiveTab(evt)))
    this.events.push(bind(this.el, this.SELECTORS.MAP_TOP, 'click', (evt) => this.focusBottom(evt)))
    this.events.push(bind(this.el, this.SELECTORS.MAP_BOTTOM, 'click', (evt) => this.focusTop(evt)))
  }

  /* ---------- EVENTS ---------- */

  focusToActiveTab(evt) {
    let activeTab = select('.boutique-tabs .selected')
    activeTab.focus()
  }

  focusBottom() {
    select(this.SELECTORS.MAP_BOTTOM).focus()
  }

  focusTop() {
    select(this.SELECTORS.MAP_TOP).focus()
  }

  showTabContent(evt) {
    if (isMobileScreen) {
      this.boutiqueInfo = select(this.SELECTORS.BOUTIQUE_INFO)
      this.mapTab = select(this.SELECTORS.MAP_TAB) || null
      this.listTab = select(this.SELECTORS.LIST_TAB) || null
      if (evt && evt.target && evt.target.classList.contains('map-tab')) {
        this.boutiqueMap ? this.boutiqueMap.removeAttribute('hidden') : ''
        this.mapTab ? this.mapTab.setAttribute('aria-current', 'true') : ''
        this.listTab ? this.listTab.removeAttribute('aria-current') : ''
        this.boutiqueList ? this.boutiqueList.setAttribute('hidden', '') : ''
        this.mapTab ? this.mapTab.classList.add('selected') : ''
        this.listTab ? this.listTab.classList.remove('selected') : ''
        this.boutiqueInfo ? this.boutiqueInfo.remove() : ''
        let buttonNavigateTop = select('.map-button-navigate-to-top')
        buttonNavigateTop ? buttonNavigateTop.remove() : ''
        this.createBoutiqueInfoBox()
        let boutiqueListGoBack = select('p[role=heading]', this.boutiqueMap)
        boutiqueListGoBack ? focusElement(boutiqueListGoBack) : ''
        this.master.analyticsHandler('oab-when-user-select-boutique-tab', {
          eventAction: this.master.oabDataObject.service.title,
          eventLabel: 'map_display'
        })
      } else {
        this.boutiqueList ? this.boutiqueList.removeAttribute('hidden') : ''
        this.listTab ? this.listTab.setAttribute('aria-current', 'true') : ''
        this.mapTab ? this.mapTab.removeAttribute('aria-current') : ''
        this.boutiqueMap ? this.boutiqueMap.setAttribute('hidden', '') : ''
        this.listTab ? this.listTab.classList.add('selected') : ''
        this.mapTab ? this.mapTab.classList.remove('selected') : ''
        let boutiqueListGoBack = select('p[role=heading]', this.boutiqueList)
        boutiqueListGoBack ? focusElement(boutiqueListGoBack) : ''
        if (this.selectedListItem) {
          const scrollPos = this.selectedListItem.offsetTop - this.selectedListItem.parentNode.offsetTop
          this.LIST.scrollTo({
            top: scrollPos,
            behavior: 'smooth'
          })
        }
        this.master.analyticsHandler('oab-when-user-select-boutique-tab', {
          eventAction: this.master.oabDataObject.service.title,
          eventLabel: 'list_display'
        })
      }
    }
  }

  createBoutiqueInfoBox() {
    if(this.selectedBoutique.address.postcode != null && this.selectedBoutique.address.address4 != null){
      this.address = `${this.selectedBoutique.address.address1 ? this.selectedBoutique.address.address1 : ''} -
      ${this.selectedBoutique.address.postcode ? this.selectedBoutique.address.postcode : ''}
      ${this.selectedBoutique.address.address4 ? this.selectedBoutique.address.address4 : ''}`
    }else{
      this.address = `${this.selectedBoutique.address.address1 ? this.selectedBoutique.address.address1 : ''}`
    }
    this.boutiqueTemplate = `<div class = "js-boutiqueInfo boutiqueInfo" aria-live="polite">
    <p role="heading" aria-level="3" class="heading is-5" tabindex="-1"><span class="is-sr-only" role="presentation">${config.oab.boutiqueSelection.boutiqueInfoBox}</span>${this.selectedBoutique.name}</p>
    <p>${this.address}</p>
    </div>
    <button type="button" class="button-navigate-to-top map-button-navigate-to-top is-sr-only is-sr-only-focusable">${config.oab.boutiqueSelection.backToActiveTabText}</button>`
    let boutiqueMapBottom = select('#skip_storemap_bottom')
    boutiqueMapBottom.parentElement.insertAdjacentHTML("afterend", this.boutiqueTemplate)
  }

  focusInCallback(evt) {
    evt.preventDefault()
    const list = selectAll(this.SELECTORS.ITEMS_RADIO)
    if (list) {
      list.forEach(element => {
        if (evt.type === 'click' && element.parentElement.parentElement === evt.delegateTarget) {
          element.parentElement.parentElement.classList.toggle('is-focused', true)
          element.parentElement.parentElement.classList.toggle('selected', true)
          element.checked = true
          this.selectedBoutique = this.data[element.value]
          this.selectedBoutique.boutiqueIndex = parseInt(element.value)
          return true
        } else if (evt.type === 'focusin' && element === evt.delegateTarget) {
          element.parentElement.parentElement.classList.toggle('is-focused', true)
          element.parentElement.parentElement.classList.toggle('selected', true)
          element.checked = true
          this.selectedBoutiqueItem = select(`${this.SELECTORS.LIST} .selected`)
          if (this.selectedBoutiqueItem) {
            const scrollPos = this.selectedBoutiqueItem.offsetTop - this.selectedBoutiqueItem.offsetHeight
            if(isMobileScreen()) {
              this.selectedBoutiqueItem.scrollIntoView({
                block: "center",
                behavior: "smooth"
              })
            } else {
              this.LIST.scrollTo({
                  top: scrollPos,
                  behavior: 'smooth'
              })
            }
          }
          this.selectedBoutiqueIndex++
          return true
        }

        element.parentElement.parentElement.classList.toggle('is-focused', false)
        element.parentElement.parentElement.classList.toggle('selected', false)
        element.checked = false
        return false
      }, this)
      this.loadSelectedBoutiqueMap()
      return true
    }
    return false
  }

  focusOutCallback(evt) {
    evt.delegateTarget.parentElement.parentElement.classList.toggle('is-focused', false)
  }

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
  }

  submitCallback(evt) {
    // push analytics to the master
    this.master.analyticsHandler('oab-when-user-select-boutique', {
      eventAction: this.master.oabDataObject.service.title,
      sl_searchcity: '',
      sl_searchcountry: '',
      sl_searchrequest: '',
      pos_city: this.selectedBoutique.address.address4,
      pos_country: this.selectedBoutique.address.country,
      pos_id: this.selectedBoutique.id
    })
    this.oabData.selectedBoutique = this.selectedBoutique
    this.selectedBoutique.boutiqueIndex = parseInt(select('li.selected div > input', this.el).getAttribute('value'))
    this.master.nextStep('date-selection')
    window.sessionStorage.setItem('selectedSpanAriaLabel', '')
  }

}

export default OabBoutiqueSelection
