import Base from 'modules/base'
import bind from 'delegate'
import { select } from 'lib/selectors'
import { focusElement } from 'lib/helpers'

class OabThanksMessage extends Base {
    constructor(el, master) {
        super(el)
        this.el = el
        this.master = master
        this.stepName = this.el.attributes['data-step'].value
        this.stepsArray = this.master.oabDataObject.previousStepToGo
        if (!this.stepsArray.includes(this.stepName)) {
            this.stepsArray.push(this.stepName)
        }
        this.selectedServiceTitle = this.master.oabDataObject.service.title
        this.selectedBoutique = this.master.oabDataObject.selectedBoutique
        this.selectedDateSlot = this.master.oabDataObject.selectedDateSlot
        this.SELECTORS = {
            SECTION_TITLE: '.js-thanks-message-title',
            BOUTIQUE: '.js-appointment-boutique',
            FORM_CTA: '.js-explore-link',
            DATE_ELEMENT: '.js-appointment-date',
            TIME_ELEMENT: '.js-appointment-time',
            BOUTIQUE_NAME: '.js-boutique-name',
            BOUTIQUE_ADDRESS: '.js-boutique-address',
            BOUTIQUE_TOWN: '.js-boutique-town',
            SERVICE_TITLE: '.js-selected-service-title'
        }
        this.events = []
        this.bindEvents()
        this.bindData()
        this.generateDetails(this.el)
    }

    /* ---------- BINDING ---------- */

    bindEvents() {
        this.events.push(bind(this.el, this.SELECTORS.FORM_CTA, 'click', (evt) => this.setLoader(evt)))
    }

    bindData() {
        const backButton = select('.js-step-navigation')
        this.SECTION_TITLE = select(this.SELECTORS.SECTION_TITLE) || null

        if (backButton) {
            backButton.hidden = true
        }

        if (this.SECTION_TITLE) {
            this.SECTION_TITLE.innerHTML = ''
            const title = this.master.createStepTitle(window.config.oab.thanksMessage.title, 'lblThankyoumsg')

            const array = document.title.split(' | ')
            const documentTitle = array[(array.length > 1) ? array.length - 1 : 0]
            document.title = `${window.config.oab.thanksMessage.title} | ${window.config.oab.title} | ${documentTitle}`

            this.SECTION_TITLE.insertBefore(title, this.SECTION_TITLE.childNodes[0])
            focusElement(this.SECTION_TITLE.firstChild)
        }
    }

    setLoader = () => {
        this.master.showLoader(select(this.SELECTORS.FORM_CTA, this.el), true)
    }


  generateDetails = (el) => {
    const boutiqueInfo = select('.js-boutique-info')
    const boutiqueTitle = select('.boutique-subtitle')
    const backToHomeCta = select('.back-to-home-cta')
    if (this.master.oabDataObject.service.extra.mode.toLowerCase() === 'video') {
      boutiqueInfo.setAttribute('hidden', '')
      boutiqueTitle.setAttribute('hidden', '')
      backToHomeCta.classList.add('virtual-service-thanks-cta')
    }
    else {
      boutiqueInfo.removeAttribute('hidden')
      boutiqueTitle.removeAttribute('hidden')
      backToHomeCta.classList.remove('virtual-service-thanks-cta')
      const boutiqueNameEl = select(this.SELECTORS.BOUTIQUE_NAME, el)
      const boutiqueAddressEl = select(this.SELECTORS.BOUTIQUE_ADDRESS, el)
      const boutiqueTownEl = select(this.SELECTORS.BOUTIQUE_TOWN, el)
      if (boutiqueNameEl) boutiqueNameEl.innerHTML = this.selectedBoutique.name
      if (boutiqueAddressEl) boutiqueAddressEl.innerHTML = this.selectedBoutique.address.address1
      if(this.selectedBoutique.address.postcode != null && this.selectedBoutique.address.address4 != null){
      if (boutiqueTownEl) boutiqueTownEl.innerHTML = `${this.selectedBoutique.address.postcode} ${this.selectedBoutique.address.address4}`
    }else{
        boutiqueTownEl.parentElement.removeChild(boutiqueTownEl)
    }
    }
    const selectedService = select(this.SELECTORS.SERVICE_TITLE, el)
    const lang = document.documentElement.lang
    const dateEl = select(this.SELECTORS.DATE_ELEMENT, el)
    const timeEl = select(this.SELECTORS.TIME_ELEMENT, el)
    const selectedTime = this.selectedDateSlot.date.time
    const dateArray = selectedTime.split(/[^0-9]/)
    this.date = new Date(dateArray[0], dateArray[1] - 1, dateArray[2], dateArray[3], dateArray[4], dateArray[5])
    if (selectedService) selectedService.innerHTML = this.selectedServiceTitle
    if (dateEl && timeEl) {
      dateEl.innerHTML = this.date.toLocaleDateString(lang, {weekday: 'long', month: 'long', day: 'numeric'})
    	timeEl.innerHTML = this.date.toLocaleTimeString(lang, {hourCycle: 'h23', hour: '2-digit', minute: '2-digit'})
}
  }

    destroy = () => {
        this.events.forEach(evt => {
            evt.destroy()
        })
    }

}

export default OabThanksMessage