import Base from 'modules/base'
import bind from 'delegate'
import config from 'lib/config'
import { select, getCollection } from 'lib/selectors'
import OAB_API_MANAGER from './oab-api-manager'
import { focusElement } from 'lib/helpers'
import notifier from 'modules/notifier'
import pageTitle from 'modules/pageTitle'

const COLLECTION_CATEGORY_TITLE = '.js-collection-category-title'
const STEP_CONTAINER = '.js-collection-category-container'
const RESPONSE_SET = '.js-response-list'

class OabCollectionCategory extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.selectedAxis = this.master.oabDataObject.selectedAxis
    this.events = []
    this.init()
    this.bindEvents()
  }

  init() {
    this.companyId = this.master.oabDataObject.companyId
    this.questionSetId = this.master.oabDataObject.service.questionSetId
    this.selectedServiceDescription = this.master.oabDataObject.service.description
    this.serviceTitle = this.master.oabDataObject.service.title
    pageTitle(this.serviceTitle, '', ' - ')
    this.selectedQuestion = {}
    this.stepContainer = select(STEP_CONTAINER)
    this.titelContainer = select(COLLECTION_CATEGORY_TITLE)
    const titleElement = this.master.createStepTitle(this.serviceTitle, `lblTitle${this.serviceTitle.split(' ').map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join('')}`, 'h1', '1')
    this.selectedServiceTitle = this.master.oabDataObject.service.title
    this.titelContainer.appendChild(titleElement)
    this.questionListData = this.master.oabDataObject.questionListData
    focusElement(this.titelContainer.children[0])
  }

  bindEvents() {
    this.collectionCategoryCallback(this.questionListData)
    this.events.push(bind(this.el, '.js-answer-button', 'click', this.nextStepCall))
  }

  collectionCategoryCallback(data) {
    this.stepContainer.innerHTML = ''
    this.questionList = data.questionList
    if (this.questionList) {
      this.questionList.forEach((question, index) => {
        const questionClass = this.getQuestionClass(index, data.questionList.length)
        this.stepContainer.innerHTML += this.getQuestionTemplate(question, questionClass)
        const answersSet = getCollection(RESPONSE_SET, this.stepContainer)
        question.answerList.forEach((answer, answerIndex) => {
          const newResponse = document.createElement('li')
          newResponse.innerHTML = this.getResponseTemplate(answer, index, answerIndex)
          answersSet[index].appendChild(newResponse)
        })
      })
      this.master.oabDataObject.questions = [this.getSelectedQuestion(data.questionList, 0, 0)]
    }
    else if (data.errors && data.errors.error) {
      notifier.show({
        name: window.config.locales.notifier.request.label,
        message: data.errors.error || window.config.locales.notifier.request.message,
        close: window.config.locales.notifier.request.close,
      })
    }
  }

  nextStepCall = (e) => {
    const questionIndex = e.delegateTarget.getAttribute('data-question')
    const answerIndex = e.delegateTarget.getAttribute('data-answer')
    const questionData = this.getSelectedQuestion(this.questionList, questionIndex, answerIndex)
    this.master.oabDataObject.questions = [questionData]
    this.master.analyticsHandler('when-user-click-on-collection-category', {
      eventAction: this.selectedServiceTitle,
      eventLabel: questionData.answer
    })
    this.selectedServiceDescription ? this.master.nextStep('description') : this.master.nextStep('boutique-selection')
  }

  getQuestionClass(index, length) {
    if (length > 1) {
      if ((index % 2) === 0) {
        return 'left-set'
      }
      return 'right-set'
    }
    return 'central-set'
  }

  getResponseTemplate = (answer, questionIndex, answerIndex) => `<button type="button" class="button js-answer-button" data-question="${questionIndex}" data-answer="${answerIndex}">
                                        <span>${answer.answerName}</span>
                                        <div class="goto-icon">
                                          <svg class="icon is-next" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
                                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="${config.paths.icons}icons.svg${config.targetVersion}#next"></use>
                                          </svg>
                                        </div>
                                      </button>`

  getQuestionTemplate = (question, questionClass) => `
    <div class="question ${questionClass}">
        <h2 data-test='lbl${question.questionName}'>${question.questionName}</h2>
        <ul class="js-response-list">
        
        </ul>
    </div>
  `

  getSelectedQuestion = (data, questionIndex, responseIndex) => ({
    id: data[questionIndex].questionId,
    answer: data[questionIndex].answerList[responseIndex].answerName
  })

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
  }

  getBackStepAnalytics() {
    return {
      eventCategory: 'collection and category list',
      eventLabel: 'back',
      eventAction: this.selectedServiceTitle
    }
  }

}

export default OabCollectionCategory
