import Base from 'modules/base'
import bind from 'delegate'
import { select, getCollection } from 'lib/selectors'
import { focusElement, getCurrentMarket } from 'lib/helpers'
import pageTitle from 'modules/pageTitle'


const DESCRIPTION_TITLE = '.js-description-title'
const DESCRIPTION_BODY = '.js-description-body'
const SERVICE_SELECT_BUTTON = '.js-service-select'


class OabDescription extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.selectedAxis = this.master.oabDataObject.selectedAxis
    this.events = []
    this.init()
    this.fillDescriptionPage()
    this.bindEvents()
  }

  init() {
    this.descriptionTitle = select(DESCRIPTION_TITLE)
    this.descriptionBody = select(DESCRIPTION_BODY)
    this.serviceSelectionCTA = select(SERVICE_SELECT_BUTTON)
    this.master.setHeaderHeight()
  }

  fillDescriptionPage() {
    this.selectedService = this.master.oabDataObject.service
    const title = this.selectedService.title
    let duration
    pageTitle(title, '', ' - ')
    this.descriptionTitle.innerHTML = ''
    const priceDurationElement = document.createElement('span')
    priceDurationElement.classList.add('description-price', 'js-description-price', 'is-light')
    priceDurationElement.setAttribute('data-test', 'lblServicePriceAndDuration')

    const priceElement = document.createElement('span')

    const price = this.selectedService.price && this.selectedService.price.formattedValue && this.selectedService.price.value > 0 ?
      `- ${this.selectedService.price.formattedValue}` : ''

      duration = this.selectedService.duration ?
      `${this.selectedService.duration.duration}${['JP', 'TW', 'HK'].includes(window.config.country) ? '' : ' '}${window.config.oab.oabDurationUnit}` : ''

    priceElement.innerHTML = `&nbsp;${duration} ${price}`
    let currentMarket = getCurrentMarket()
    if(currentMarket === 'AE') {
      const marketElement = document.createElement('span')
      marketElement.classList.add('hidden-value')
      marketElement.innerHTML = `${currentMarket}`
      priceDurationElement.appendChild(marketElement)
    }
    priceDurationElement.appendChild(priceElement)

    const titleElement = this.master.createStepTitle(title, 'lblServiceTitle', 'h1', '2')
    titleElement.insertBefore(priceDurationElement, titleElement.firstChild)
    this.descriptionTitle.appendChild(titleElement)
    TweenLite.set(document.body, {
      delay: 0.3,
      onComplete: () => {
        focusElement(this.descriptionTitle)
      }
    })
    this.descriptionBody.innerHTML = this.selectedService.description
  }

  bindEvents() {
    this.events.push(bind(this.el, '.js-service-select', 'click', this.nextStepCall))
  }

  nextStepCall = () => {
    this.master.analyticsHandler('when-user-click-on-description-service-select', {
      eventAction: this.selectedService.title, // updated value
      eventLabel: window.config.oab.description.serviceSelectionCta, // updated valued
    })
    if (this.selectedService.extra.mode != null && this.selectedService.extra.mode.toLowerCase() === 'video') {
      this.master.nextStep('date-selection') //go to date selection if user has selected virtual service
      window.sessionStorage.setItem('selectedSpanAriaLabel', '')
    }
    else {
      this.master.nextStep('boutique-selection') // else go directly to boutique selection step
    }
  }

  getBackStepAnalytics() {
    return {
      eventCategory: 'description',
      eventLabel: 'back',
      eventAction: this.selectedService.title
    }
  }

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
  }

}

export default OabDescription
