import Base from 'modules/base'
import bind from 'delegate'
import { select, getCollection, closest } from 'lib/selectors'
import {focusElement, isMobileScreen } from 'lib/helpers'
import pageTitle from 'modules/pageTitle'
import OAB_API_MANAGER from './oab-api-manager'
import notifier from 'modules/notifier'
import config from 'lib/config'
import openAddToBagConfirmationPopin from 'lib/add-to-bag-confirmation-popin'
import popin from 'modules/popin'
let popinCloseBtn
const oabProvider = select('.js-oab-provider')
const countryArray = ['GB', 'LX', 'BR']
class OabPhoneNumber extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.selectedBoutique = this.master.oabDataObject.selectedBoutique
    this.selectedService = this.master.oabDataObject.service
    if (this.selectedService.checkoutEnabled) {
      const payableAppointment = select('.payableBooking')
      const freeAppointment = select('.freeBooking')
      if (payableAppointment) {
        payableAppointment.removeAttribute('hidden')
        freeAppointment ? freeAppointment.setAttribute('hidden','')  : ''
      }  
    }
    const buttonContinueToCheckout = select('.js-valid-number')
    buttonContinueToCheckout ? focusElement(buttonContinueToCheckout) : ''
    this.companyId = this.master.oabDataObject.companyId
    this.questionListData = this.master.oabDataObject.questionListData
    this.isLogged = JSON.parse(sessionStorage.getItem('IS_USER_AUTHENTICATED'))
    this.SELECTORS = {
      SECTION_TITLE: '.js-phone-message-title',
      FORM: '#oab-phone-form',
      SUBMIT_FORM_CTA: '.js-valid-number',
      COMMENT_SECTION: '.js-comment-section',
      COMMENT_TEXTAREA: '.js-comment-textarea',
      GUEST_MODE_DESCRIPTION: '.js-guest-mode-description',
      NEWSLETTER_CHECKBOX: 'input#oab-newsletter-subscribe'

    }
    this.events = []
    this.handleGuestMode()
    this.bindData()
    this.init()
    this.bindEvents()
    if (this.master.oabDataObject.guestModeUsed === false && this.isLogged === true) {
      this.master.oabDataObject.previousStepToGo.pop()
    }
    this.heightOfNewsletterRule = 0
  }
  
  init() {
	const form = select(this.SELECTORS.FORM)

  if(form) this.cleanErrors(form)
  window.onzoom = () => {
      if (this.master.oabDataObject.guestModeUsed === false) {
        const devicePixelRatio = window.devicePixelRatio.toFixed(2)
        const legalMention = select('.js-legal-mention-section p a')
        if (legalMention) {
          !isMobileScreen() && devicePixelRatio <= 0.67 ? legalMention.classList.add('hover-effect') : legalMention.classList.remove('hover-effect')
          !isMobileScreen() && devicePixelRatio == 0.33 ? legalMention.classList.add('hover-effect-guest') : legalMention.classList.remove('hover-effect-guest')
        }
      }
    }
  }

  // gets called by the master to get back button analytics
  getBackStepAnalytics() {
    this.commentSection.setAttribute('hidden', '')
    this.commentTextArea.value = ''
    return {
      eventCategory: 'confirm appointment',
      eventLabel: 'back',
      eventAction: this.selectedService.title
    }
  }

  /* ---------- BINDING ---------- */

  bindData() {
    this.SECTION_TITLE = select(this.SELECTORS.SECTION_TITLE) || null
    if (this.SECTION_TITLE) {
      this.SECTION_TITLE.innerHTML = ''
      const title = this.master.createStepTitle(window.config.oab.phoneNumber.title, 'lblBookAnAppointment')
      pageTitle(window.config.oab.phoneNumber.title, '', ' - ')
      this.SECTION_TITLE.insertBefore(title, this.SECTION_TITLE.childNodes[0])
      focusElement(this.SECTION_TITLE.firstChild)
    }

    this.FORM = select(this.SELECTORS.FORM) || null
    this.commentTextArea = select(this.SELECTORS.COMMENT_TEXTAREA, this.FORM) || null
    this.FORM.removeAttribute('action')
    this.submitBtn = select(this.SELECTORS.SUBMIT_FORM_CTA, this.FORM)
    this.commentSection = select(this.SELECTORS.COMMENT_SECTION, this.FORM)
    
    if (this.questionListData && this.questionListData.questionList) {
      this.questionListData.questionList.forEach((question) => {
        if (question.questionName.toLowerCase() === 'comment') {
          this.commentId = question.questionId
          this.commentAnswer = question.answerList
        }
        else {
          this.wfjQuestId = question.questionId
        }
      })
      if (this.commentAnswer === null)  this.commentSection.removeAttribute('hidden')
    }
	else if(oabProvider.innerText === "SALESFORCE"){
	this.commentSection.removeAttribute('hidden')
}
  }

  bindEvents() {
    this.events.push(bind(this.el, this.SELECTORS.SUBMIT_FORM_CTA, 'click', (evt) => this.submitFormCallback(evt)))
    this.events.push(bind(this.el, this.SELECTORS.NEWSLETTER_CHECKBOX, 'change', (evt) => this.handleNewletterSetting(evt)))
  }

  handleNewletterSetting = (evt) => {
    if (countryArray.includes(config.country) || config.region === "apac") {
      const newsletterElm = evt.delegateTarget

      if (newsletterElm) {
        const newsletterRulesElm = select('div#oab-newsletter-rules')
        const isChecked = newsletterElm.checked
        isChecked && newsletterRulesElm.hasAttribute('hidden') ? newsletterRulesElm.removeAttribute('hidden') : newsletterRulesElm.setAttribute('hidden', '')
      

      if (!this.heightOfNewsletterRule) {
        newsletterRulesElm.classList.remove('is-hidden')
        this.heightOfNewsletterRule = 'auto'
        newsletterRulesElm.style.height = 0
      }

      TweenLite.fromTo(newsletterRulesElm, (isChecked ? 1 : .4),
        { autoAlpha: isChecked ? 0 : .5, height: isChecked ? 0 : this.heightOfNewsletterRule },
        { autoAlpha: isChecked ? 1 : .0, height: isChecked ? this.heightOfNewsletterRule : 0 }
        )
      }
    }
  }
  
  cleanErrors(element) {
    const hasErrorClasses = getCollection('.has-error', element)
	  const errorMessages = getCollection('.error', element)
	  for (let i = 0; i < hasErrorClasses.length; i++) {
	    hasErrorClasses[i].classList.remove('has-error')
	  }
	  for (let i = 0; i < errorMessages.length; i++) {
	    errorMessages[i].parentNode.removeChild(errorMessages[i])
	  }
	  select(this.SELECTORS.SUBMIT_FORM_CTA).removeAttribute('aria-describedby')
  }

  submitFormCallback = (evt) => {
    const form = select(this.SELECTORS.FORM)
    if(form) this.cleanErrors(form)
    if (this.FORM.checkValidity()) {
      evt.preventDefault()
      const countryCodeSelect = select('select[name=countryCode]', this.FORM)
      const phoneNumberInput = select('input[type=tel]', this.FORM)
      const email = select('input[type=email]', this.FORM)
      const firstName = select('.js-name-firstName input', this.FORM)
      const lastName = select('.js-name-lastName input', this.FORM)
      const interestsSection = select('.js-interests-fields')
      const interestsCheckbox = getCollection('.js-interests-checkbox-input input')
      const wfjInterests = []
      const newsletterCheckbox = select('.js-newsletter-section input')
      if(this.master.oabDataObject.interests && interestsCheckbox) {
        interestsCheckbox.forEach(checkbox => {
          if(checkbox.checked) {
            wfjInterests.push(checkbox.value)
          }
        })
      }
      
      if (countryCodeSelect && phoneNumberInput && this.commentTextArea && email && firstName && lastName && interestsSection) {
        this.phoneNumberValue = `${countryCodeSelect.value}${phoneNumberInput.value}`
        this.countryCodeVal = countryCodeSelect.value
        this.phoneNumberVal = phoneNumberInput.value
        this.commentValue = this.commentTextArea.value
        this.firstNameValue = firstName.value
        this.lastNameValue = lastName.value
        this.emailValue = email.value
        let params
        let apiURL
        if (this.master.oabDataObject.service.associatedEvents) {
          if (this.master.oabDataObject.guestModeUsed === true) {

          }
        }
        let checkoutRequired = this.selectedService.checkoutEnabled ;
        let guestModeUsed = this.master.oabDataObject.guestModeUsed;
        let associatedEventPresent = this.master.oabDataObject.service.associatedEvents;
        // Guest User
        if (guestModeUsed) {
          params = {
            boutiqueId: this.selectedBoutique.id,
            firstname: this.firstNameValue,
            lastName: this.lastNameValue,
            emailAddress: this.emailValue,
            phoneNumber: this.phoneNumberValue,
            questionId: this.commentId || 0,
            answer: this.commentValue || '',
            wfjQuestionId: this.wfjQuestId || 0,
            wfjAnswers: wfjInterests,
            divisionType: this.master.oabDataObject.selectedDivision,
            newsLetterOptin: newsletterCheckbox && newsletterCheckbox.checked ? true : false
          }

          if(oabProvider.innerText === "SALESFORCE"){ 
            params.countryCode = this.countryCodeVal,
            params.phoneNumber = this.phoneNumberVal,
            params.availabilityId = this.master.oabDataObject.selectedDateSlot.date.serviceAvailId,
            params.sfMarketId = this.master.oabDataObject.selectedDateSlot.date.marketCode,
            params.storeCode = this.selectedBoutique.sfBoutiqueCode
          }
         
          if (associatedEventPresent) {
            params = Object.assign(params, {
              date: this.master.oabDataObject.selectedDateSlot.date.time,
              eventId: this.master.oabDataObject.selectedDateSlot.date.timeId,
              eventChainId: this.master.oabDataObject.selectedBoutique.eventId
            });
          } else {
            params = Object.assign(params, {
              date: this.master.oabDataObject.selectedDateSlot.date.time.substring(0, this.master.oabDataObject.selectedDateSlot.date.time.indexOf("T")),
              timeId: this.master.oabDataObject.selectedDateSlot.date.timeId,
              serviceId: this.master.oabDataObject.service.serviceId,
              personId: this.master.oabDataObject.selectedDateSlot.date.adviserId
            });
          }
          apiURL = associatedEventPresent ? OAB_API_MANAGER.postAppointmentGuestEventBookingConfirmation : OAB_API_MANAGER.postAppointmentGuestBookingConfirmation;
        } else {
          // Registered User
          params = {
            memberId: this.master.oabDataObject.addedBouking.memberId,
            headerAuthToken: this.master.oabDataObject.addedBouking.headerAuthToken,
            boutiqueId: this.selectedBoutique.id,
            phoneNumber: this.phoneNumberValue,
            questionId: this.commentId || 0,
            answer: this.commentValue || ''
          }
          if (!associatedEventPresent) {
            params = Object.assign(params, {
              companyId: this.companyId
            })
          }
          apiURL = associatedEventPresent ? OAB_API_MANAGER.postAppointmentEventBookingConfirmation : OAB_API_MANAGER.postAppointmentBookingConfirmation;
        }
        if (checkoutRequired) {
          params = Object.assign(params, {
            price: this.selectedService.price.value,
            currency: this.selectedService.price.currencyIso,
            guestModeUsed: guestModeUsed,
            eventBooking: associatedEventPresent,
            serviceId: this.selectedService.serviceId,
            eventName: this.selectedService.title,
            selectedDate: this.master.oabDataObject.selectedDateSlot.date.time,
            location: this.master.oabDataObject.selectedBoutique.name,
            eventMode: this.master.oabDataObject.service.extra.mode,
            isOverrideCart: evt.currentTarget.dataset.overrideCart ? false : true
          })

          
          apiURL = OAB_API_MANAGER.postAppointmentStartCheckout
        }

        this.master.showLoader(this.submitBtn, true)
        apiURL(params)
          .then(data => {
            if (!data.errors || !data.errors.error) {
              if (checkoutRequired) {
                let dimension45Value = 'none'
                if(data.checkoutEntry !== null) {
                if(data.checkoutEntry.product.exclusive){
                  dimension45Value = 'Exclusive'
                }else if(data.checkoutEntry.product.newProduct){
                  dimension45Value = 'New'
                }else if(data.checkoutEntry.product.limitedEdition){
                  dimension45Value = 'Limited Edition'
                }else if(data.checkoutEntry.product.foreignEdition){
                  dimension45Value = 'Foreign Edition'
                }else if(data.checkoutEntry.product.numberedEdition){
                  dimension45Value = 'Numbered Edition'
                }
              }

                if (data.showpopin === true) {
                  
                  openAddToBagConfirmationPopin('oabservice').then((modal) => {
                    this.master.destroyLoader()
                    
                    window.document.DLKit.push('on-tracking', {
                     'eventCategory': 'mix cart pop in',
                     'eventAction': 'display',
                     'eventLabel': '',
                     'event': 'nievent'
                   })
                     popinCloseBtn = select('.js-popin-close', modal.modal.el)
                     if(popinCloseBtn) popinCloseBtn.addEventListener('click', () => {
                       window.document.DLKit.push('on-tracking', {
                         'eventCategory': 'mix cart pop in',
                         'eventAction': 'leave',
                         'eventLabel': '',
                         'event': 'uaevent'
                       })
                     })
                     focusElement(popinCloseBtn, {notChangeTabIndex: true })
                     modal.modal.el.addEventListener('click', (e) => {
                       if (closest(e.target, '.popin__row')) return
                       popin.hide()
                     })
                     const confirmationPopinCancelCta = select('.js-confirmation-cancel-cta')
                     if (confirmationPopinCancelCta) confirmationPopinCancelCta.addEventListener('click', () => {
                       popin.hide()
                       window.document.DLKit.push('on-tracking', {
                         'eventCategory': 'mix cart pop in',
                         'eventAction': 'cancel',
                         'eventLabel': '',
                         'event': 'uaevent'
                       })
                     })
                     const confirmationPopinCta = select('.js-confirmation-popin-cta')
                     
                     if (confirmationPopinCta) confirmationPopinCta.addEventListener('click', (ev) => {
                       this.submitFormCallback(ev)
                       popin.hide()
                       window.document.DLKit.push('on-tracking', {
                         'eventCategory': 'mix cart pop in',
                         'eventAction': 'confirm',
                         'eventLabel': '',
                         'event': 'uaevent'
                       })
                     })
                   })
                } else{
                  dataLayer.push({
                    event: 'uaevent',
                    eventCategory: 'add to cart',
                    eventAction: 'regular quickbuy',
                    eventLabel: this.selectedService.title,
                    cartId: data.cartId,
                    ecommerce: {
                      currencyCode: window.dataLayerGA.pageLoad.currencyCode,
                      add: {
                        products: [{
                          name: this.selectedService.title.toLowerCase(),
                          id: data.checkoutEntry.product.code,
                          price: this.selectedService.price.value,
                          brand: data.checkoutEntry.product.division,
                          category: data.checkoutEntry.product.division + "/booking/" + this.selectedService.title.toLowerCase(),
                          variant: data.checkoutEntry.product.gaVariant,
                          quantity: 1,
                          dimension8: data.checkoutEntry.product.code,
                          dimension17: 'no',
                          // dimension18: Product Status
                          // dimension19:  Sub Collection
                          dimension20:  data.checkoutEntry.product.productLineCategory,
                          dimension21:  'in stock',
                          dimension25:  data.checkoutEntry.product.axisType,
                          dimension45:  dimension45Value// flag
                          // dimension65: '' // Extended Catalog
                        }]
                      }
                    }
                  })
                   window.location.href = window.config.webservices.checkout.oabCheckoutUrl
                }

               
              } 
              else {
                this.master.destroyLoader()
                this.master.analyticsHandler('oab-when-user-valid-phone', {
                  eventAction: this.selectedService.title,
                  sl_searchcity: '',
                  sl_searchcountry: '',
                  sl_searchrequest: '',
                  pos_city: this.selectedBoutique.address.address4,
                  pos_country: this.selectedBoutique.address.country,
                  pos_id: this.selectedBoutique.id
                })
                this.master.nextStep('thanks-message')
                this.master.analyticsHandler('oab-when-user-see-thanks-message', {
                  eventAction: this.selectedService.title,
                })
                if(data.subscriptionError === "") {
                  this.master.analyticsHandler('oab-when-user-select-newsletter-subscription', {})
                }}
            } else if (data.errors &&  data.errors.error === 'Please note : An account already exists with this email') {
                this.master.destroyLoader()
                const errorMsg = select('#guest-confirmation-error')
            	  errorMsg.classList.add('error', 'guest-confirmation-error')
            	  const ctaDescribedBy = select('.js-valid-number')
            	  if (ctaDescribedBy) ctaDescribedBy.setAttribute('aria-describedby', 'guest-confirmation-error')
            	  errorMsg.innerHTML = config.locales.errors.form.fields.guestUserEmail
            	  focusElement(errorMsg)
            	  this.stepsArray = this.master.oabDataObject.previousStepToGo
                if (this.stepsArray.includes(this.stepName)) {
                  this.stepsArray.pop(this.stepName)
                }
              } else {
              this.master.destroyLoader()
              this.master.displayErrorModalWindow()
            }
          })
          .catch(error => {
            this.master.destroyLoader()
            this.master.displayErrorModalWindow()
          })
      }
    }
  }

  handleGuestMode = () => {
    const guestModeDescription = select(this.SELECTORS.GUEST_MODE_DESCRIPTION)
    const phoneDescription = select('.js-phone-description')
    const nameFields = select('.js-name-fields')
    const mailFields = select('.js-mail-fields')
    const inputPhone = select('.js-input-phone')
    const phoneNumberInput = select('input[type=tel]', this.FORM)
    const inputCountryCode = select('.js-input-countryCode .selectbox__input')
    const commentLabel = select('.js-review-field-label')
    const commentTextarea = select('.js-comment-textarea')
    const commentDescription = select('.js-comment-description')
    const confirmBookingCta = select('.js-confirmation-cta-text')
    const firstNameField = select('.js-name-firstName input')
    const lastNameField = select('.js-name-lastName input')
    const mailInputField = select('.mail-input input')
    const phoneNumberTooltip = select('.is-guest-oab-phoneNumber-tooltip')
    const phoneCountryCode = select('.js-input-countryCode')
    const interestsSection = select('.js-interests-fields')
    const options = getCollection('.js-input-countryCode .selectbox__input option')
    const selectboxSpan = select('.phone_selectbox span')
    const interestsCheckbox = getCollection('.js-interests-checkbox-input input')
    const inputPhoneLabel = select('.js-label-input', inputPhone)
    interestsCheckbox.forEach(checkbox => {
      checkbox.removeAttribute('disabled')
    })
    const newsletterCheckbox = select('.js-newsletter-section')

    if (interestsSection && guestModeDescription && phoneDescription && nameFields && mailFields && inputPhone && inputCountryCode && commentLabel && commentTextarea && commentDescription && confirmBookingCta && options && selectboxSpan && phoneNumberInput && inputPhoneLabel) {
      if (this.master.oabDataObject.guestModeUsed === true) {
        guestModeDescription.removeAttribute('hidden')
        phoneDescription.setAttribute('hidden', '')
        nameFields.removeAttribute('hidden')
        mailFields.removeAttribute('hidden')
        inputPhone.setAttribute('autocomplete', 'tel-national')
        inputCountryCode.removeAttribute('autocomplete')
        inputCountryCode.setAttribute('autocomplete', 'tel-country-code')
        commentLabel.setAttribute('hidden', '')
        commentTextarea.setAttribute('placeholder', config.oab.phoneNumber.reviewPlaceHolder)
        commentDescription.classList.remove('is-sr-only')
        confirmBookingCta.innerText = config.oab.phoneNumber.guestModeConfirmationCta
        firstNameField.setAttribute('required', true)
        lastNameField.setAttribute('required', true)
        mailInputField.setAttribute('required', true)
        phoneNumberTooltip.removeAttribute('hidden')
        phoneCountryCode.classList.add('phone-tooltip')
        newsletterCheckbox ? newsletterCheckbox.removeAttribute('hidden') : ''
        this.master.oabDataObject.interests ? interestsSection.removeAttribute('hidden') : interestsSection.setAttribute('hidden', '')
      } else {
        const oabPhoneInfo = this.master.oabDataObject.oabPhoneInfo
        if (oabPhoneInfo) {
          options.forEach(function(el) {
            el.hasAttribute('selected') ? el.removeAttribute('selected') : ''
            el.innerText == oabPhoneInfo.phoneDiallingCode ? el.setAttribute('selected', 'selected') : ''
          })
          selectboxSpan.innerText = oabPhoneInfo.phoneDiallingCode
          phoneNumberInput.value = oabPhoneInfo.phoneNumber
          inputPhoneLabel.classList.add('has-content')
        }
        guestModeDescription.setAttribute('hidden', '')
        phoneDescription.removeAttribute('hidden')
        nameFields.setAttribute('hidden', '')
        mailFields.setAttribute('hidden', '')
        inputPhone.setAttribute('autocomplete', 'tel-national')
        inputCountryCode.setAttribute('autocomplete', 'country-code')
        commentLabel.removeAttribute('hidden')
        commentTextarea.removeAttribute('placeholder')
        commentDescription.classList.add('is-sr-only')
        confirmBookingCta.innerText = config.oab.phoneNumber.confirmationCta
        phoneNumberTooltip.setAttribute('hidden', '')
        phoneCountryCode.classList.remove('phone-tooltip')
        interestsSection.setAttribute('hidden', '')
        newsletterCheckbox ? newsletterCheckbox.setAttribute('hidden', '') : ''
      }
    }
  }


  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
  }

}

export default OabPhoneNumber
