import Base from 'modules/base'
import bind from 'delegate'
import { select } from 'lib/selectors'
import config from 'lib/config'
import * as xhr from 'modules/fetch'
import OAB_API_MANAGER from './oab-api-manager'
import notifier from 'modules/notifier'
import pageTitle from 'modules/pageTitle'
import {Manager, YAPI_PAGELOAD_RECEIVED} from 'lib/events'


class OabRedirectAccount extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.SELECTORS = {
      SECTION: '.oab-redirect',
      SECTION_TITLE: '.js-redirect-title',
      CTA: '.oab-myacnt-btn',
      GUEST_MODE_CTA: '.js-guest-mode-cta'
    }
    this.events = []
    this.selectedService = this.master.oabDataObject.service
    this.dataWS = this.getDataToPass()

    window.scrollTo(0, 0)
    this.master.showLoader(this.el, false, 0, () => {
      const loader = select('.loader', this.el)
      loader.style.width = '100%'
    })

    if (this.master.oabDataObject.wasLogged === true && !this.master.oabDataObject.isBackButtonClicked && window.sessionStorage.getItem('isRedirectedToMyAccount') === 'true') {
      Manager.subscribe(YAPI_PAGELOAD_RECEIVED, () => {
        this.isUserLogged()
      })
    }
    else {
      this.isUserLogged()
    }
  }

  getDataToPass() {
    try {
      const dateObject = new Date(this.master.oabDataObject.selectedDateSlot.date.time)
      let dataWS
      if (this.master.oabDataObject.service.associatedEvents) {
        dataWS = {
          eventId: this.master.oabDataObject.selectedDateSlot.date.timeId,
          eventChainId: this.master.oabDataObject.selectedBoutique.eventId,
          date: this.master.oabDataObject.selectedDateSlot.date.time,
          boutiqueId: this.master.oabDataObject.selectedBoutique.id,
          divisionType: this.master.oabDataObject.selectedDivision
        }
      } else {
        dataWS = {
        serviceId: this.selectedService.serviceId,
        date: `${dateObject.getFullYear()}-${dateObject.getMonth() + 1}-${dateObject.getDate()}`,
        personId: this.master.oabDataObject.selectedDateSlot.date.adviserId,
        timeId: this.master.oabDataObject.selectedDateSlot.date.timeId,
        boutiqueId: this.master.oabDataObject.selectedBoutique.id,
        questionId: 0,
        answer: '',
        divisionType: this.master.oabDataObject.selectedDivision
      }
    }
      if (this.master.oabDataObject.questions) {
        dataWS.questionId = this.master.oabDataObject.questions[0].id
        dataWS.answer = this.master.oabDataObject.questions[0].answer
      }
      return dataWS
    }
    catch(e) {
      this.master.nextStep('carousel')
    }
  }

  displaySection() {
    this.master.destroyLoader()
    const section = select(this.SELECTORS.SECTION)
    if (section) section.style.opacity = '1'
  }
  isUserLogged() {
    TweenLite.set(document.body, {
      delay: 3,
      onComplete: () => {
        const isLogged = JSON.parse(window.sessionStorage.getItem('IS_USER_AUTHENTICATED'))
        let apiURL
        apiURL = this.master.oabDataObject.service.associatedEvents ? OAB_API_MANAGER.addEventAppointmentBooking : OAB_API_MANAGER.addAppointmentBooking
        if (isLogged && isLogged === true) {
          apiURL(this.dataWS)
            .then(data => {
              this.master.destroyLoader()
              if (!data.errors) {
                this.master.oabDataObject.addedBouking = {
                  headerAuthToken: data.headerAuthToken,
                  memberId: data.memberId
                }
                this.master.oabDataObject.oabPhoneInfo = data.oabPhone
                this.master.oabDataObject.guestModeUsed = false
                this.master.nextStep('phone-number')
              }
              else {
                this.master.displayErrorModalWindow()
                this.master.nextStep('date-selection')
              }

            })
            .catch(error => {
              this.master.destroyLoader()
              this.displaySection()
              this.master.displayErrorModalWindow()
            })
        }
        else {
          this.displaySection()
          this.bindEvents()
          this.bindData()
          this.master.setHeaderHeight()
        }
      }
    })
  }

  // gets called by the master to get back button analytics
  getBackStepAnalytics() {
    return {
      eventCategory: 'finalize appointment',
      eventAction: this.selectedService.title
    }
  }

  bindEvents() {
    this.events.push(bind(this.el, this.SELECTORS.CTA, 'click', this.redirectToMyAccount))
    this.events.push(bind(this.el, this.SELECTORS.GUEST_MODE_CTA, 'click', this.redirectToGuestMode))
  }

  bindData() {
    this.SECTION_TITLE = select(this.SELECTORS.SECTION_TITLE) || null

    if (this.SECTION_TITLE) {
      this.SECTION_TITLE.innerHTML = ''
      const title = this.master.createStepTitle(window.config.oab.redirectAccount.title)
      this.SECTION_TITLE.appendChild(title)
      title.focus()
    }
    pageTitle(window.config.oab.redirectAccount.title, '', ' - ')
  }

  redirectToGuestMode = () => {
    this.master.oabDataObject.guestModeUsed = true
    this.master.analyticsHandler('oab-when-user-goto-guest-mode', {
      eventAction: this.selectedService.title,
    })
    this.master.nextStep('phone-number')
  }

  redirectToMyAccount = () => {
    sessionStorage.setItem('oab-data', JSON.stringify(this.master.oabDataObject))
    this.master.analyticsHandler('oab-when-user-goto-account', {
      eventAction: this.selectedService.title,
    })
    this.master.showLoader(select(this.SELECTORS.CTA, this.el), true, 1000, () => {
      TweenLite.set(document.body, {
        delay: 3,
        onComplete: () => {
          this.master.oabDataObject.guestModeUsed = false
          this.redirect(this.dataWS)
        }
      })
    })
  }

  redirect(bookingData) {
	    const form = document.createElement('form')
	    form.action = this.master.oabDataObject.service.associatedEvents ? `eventLogin` : `login`
	    form.method = 'POST'
	    form.className = 'is-hidden'

	    Object.keys(bookingData).forEach(key => {
	      form.insertAdjacentHTML('beforeend', `<input type="hidden" name="${key}" value="${bookingData[key]}"/>`)
	    })
	    form.insertAdjacentHTML('beforeend', `<input type="hidden" name="CSRFToken" value="${ACC.config.CSRFToken}"/>`)
	    document.body.appendChild(form)
	    document.cookie = 'oab-login-redirect-step=true;path=/'
	    form.submit()
      window.sessionStorage.setItem('isRedirectedToMyAccount', JSON.stringify(true))
   }

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
  }

}

export default OabRedirectAccount
