import OabCarousel from './oab-carousel'
import OabCategory from './oab-category'
import OabServiceList from './oab-service-list'
import OabCollectionCategory from './oab-collection-category'
import OabDescription from './oab-description'
import OabBoutiqueSelection from './oab-boutique-selection'
import OabDateSelection from './oab-date-selection'
import OabPhoneNumber from './oab-phone-number'
import OabThanksMessage from './oab-thanks-message'
import OabRedirectAccount from './oab-redirect-account'
import { select, getCollection } from 'lib/selectors'
import Loader from 'ui/widgets/loader'
import notifier from 'modules/notifier'
import pageTitle from 'modules/pageTitle'
import {OAB_NAVIGATION_BAR_HEIGHT} from 'config/constants'
import { isMobileScreen, isTabletScreen, isWideScreen, isSafari, isAndroid, isInViewPort } from 'lib/helpers'
import OAB_API_MANAGER from './oab-api-manager'

let currentStep
let currentStepIndex
let oabSteps
let stepObjects
let stepContainer
let stepperInstance
let loader
let that
let stepStart = 0

function Stepper(el) {
  if (!el) {
    return
  }
  // public functions passed to section classes



  this.stepIndex = 0
  this.stickyPosition = 0
  this.isZoomed = false
  this.oabDataObject = {
    previousStepToGo: [],
    comingFromBack: false,
    isBackButtonClicked: false,
    guestModeUsed: false,
    bannerData: {
      image: null,
      description: null
    }
  }

  this.stepNavigation = select('.js-step-navigation')
  this.stepperNavigation = select('.js-step-navigation', el)
  this.loaderExist = false

  this.nextStep = (step) => {
    this.oabDataObject.comingFromBack = false
    currentStepIndex = getUpdatedIndex()
    if (getStepIndex(oabSteps, step) === stepStart) that.stepperNavigation.setAttribute('hidden', '')
    else {
      that.stepperNavigation.removeAttribute('hidden')
      const stepperBackNavigation = select('p', this.stepperNavigation)
      if (stepperBackNavigation) {
        if (!isMobileScreen() && window.devicePixelRatio >= 1.25) {
          stepperBackNavigation.classList.add('zoomed-navigation')
        } else {
          stepperBackNavigation.classList.remove('zoomed-navigation')
        }
        let header = select('.is-fixed', that.stepperNavigation)
        if (isTabletScreen() && header && (header.style.transform === "")) {
          stepperBackNavigation.classList.add('tablet-transform')
        } else {
            stepperBackNavigation.classList.remove('tablet-transform')
        }
      }
    }
    const titleToHide = select('.js-step-dynamic-title', oabSteps[currentStepIndex])
    if (titleToHide) titleToHide.parentNode.removeChild(titleToHide)
    if (currentStepIndex < (oabSteps.length - 1)) {
      oabSteps[currentStepIndex].setAttribute('hidden', '')
      getStepIndex(oabSteps)
      showStep(getStepIndex(oabSteps, step))
    }
  }
 // push analytics
  this.showLoader = (element, isButton = false, delay = 0, callback = null) => {
    this.loaderExist = true
    loader = new Loader(element, {
      referenceEl: element,
      delay: delay,
      button: isButton
    })
    loader.init()
    loader.create(callback)
  }
  this.destroyLoader = (callback = null) => {
    if (loader) {
      loader.destroy(callback)
      this.loaderExist = false
    }

  }

  this.setHeaderHeight = () => {
    let narrowHeader = select('.js-header.is-narrow')
    if (!isMobileScreen()) {
      select('.js-step-navigation').classList.contains('js-sticky-item') ? '' : select('.js-step-navigation').classList.add('js-sticky-item')
      const stickyHeader = select('.js-step-navigation.js-sticky-item')
      const stepperBackNavigation = select('p', this.stepperNavigation)
      if (stickyHeader) stickyHeader.style.height = `${OAB_NAVIGATION_BAR_HEIGHT}px` // override sticky item height for OAB navigation element
      const stickyHeaderDiv = select('.is-static', stickyHeader)
      const stickyHeaderFixed = select('.is-fixed', stickyHeader)
      if(narrowHeader && stickyHeaderDiv && stickyHeaderDiv.style.transform) {
        stickyHeaderDiv.classList.remove('is-static')
        stickyHeaderDiv.classList.add('is-fixed')
        stickyHeaderDiv.style.transform = !stepperBackNavigation.classList.contains('tablet-transform') ?  "translateY(80px)" : ''
      }
      else {
        if (stickyHeaderFixed)
          stickyHeaderFixed.style.transform = narrowHeader && !stepperBackNavigation.classList.contains('tablet-transform') ?  "translateY(80px)" : stickyHeaderFixed.style.transform
      
          if(stickyHeaderFixed && stickyHeaderFixed.style.transform != '' && stepperBackNavigation.classList.contains('tablet-transform')) {
            stepperBackNavigation.classList.remove('tablet-transform')
          }
        }
    }
  }

  this.analyticsHandler = (analyticsName, data) => {
    window.document.DLKit.push(analyticsName, data)
  }
  this.createStepTitle = (title, dataTest = null, titleLevel = 'h1', titleSize = '1') => {
    const titleElement = document.createElement(titleLevel)
    titleElement.classList.add('heading', `is-${titleSize}`, 'js-step-dynamic-title', 'step-dynamic-title')
    titleElement.setAttribute('tabindex', '-1')
    if (dataTest) titleElement.setAttribute('data-test', dataTest)
    const titleSpan = document.createElement('span')
    titleSpan.innerText = title
    titleElement.appendChild(titleSpan)
    return titleElement
  }

  this.generateBanner = (currenThis) => {
    const bannerImage = select('.js-axis-banner', currenThis)
    const bannerData = this.oabDataObject.bannerData
    if (bannerImage && bannerData.image) bannerImage.innerHTML = bannerData.image
    const bannerDescription = select('.js-axis-banner-description', currenThis)
    if (bannerDescription && bannerData.description !== null && bannerData.description !== '') {
      bannerDescription.removeAttribute('hidden')
      bannerDescription.innerHTML = bannerData.description
    }
  }

  this.scrollToTop = () => {
    window.scrollTo(0,0)
  }

  this.visibleHeader = () => {
    let headerOuterEl = select('.header__outer')
    if(headerOuterEl != null) {
    let headerOuterElStyle = getComputedStyle(headerOuterEl)
    if(headerOuterElStyle.position != "fixed")
        headerOuterEl.style.position = "fixed"
    }
  }

  this.setWhiteBanner = () => {
    let bnrImage = select('.js-axis-banner img', oabSteps[this.stepIndex])
    if(!isMobileScreen() && bnrImage != null) {
      if (this.stepNavigation) this.stepNavigation.removeAttribute('hidden')
      select('.js-back-btn', oabSteps[this.stepIndex]).setAttribute('hidden', '')
      this.stickyPosition = 0
    }
  }
  
 this.bnrButton = () => {
    let bnrImage = select('.js-axis-banner img', oabSteps[this.stepIndex])
    if(!isMobileScreen() && this.oabDataObject.bannerData && this.oabDataObject.bannerData.image && bnrImage != null) { 
      this.stickyPosition = 0
      this.stepNavigation.setAttribute('hidden', '')
      select('.js-back-btn', oabSteps[this.stepIndex]).removeAttribute('hidden')
    }
  }

  this.backButton = () => {
    const stepTogetBackTo = this.oabDataObject.previousStepToGo
    this.setWhiteBanner()
    stepTogetBackTo.pop()
    prevStep(stepTogetBackTo[stepTogetBackTo.length - 1])
  }

  this.displayErrorModalWindow = () => {
    notifier.show({
      name: window.config.locales.notifier.request.label,
      message: window.config.locales.notifier.request.message,
      close: window.config.locales.notifier.request.close,
    })
  }
  that = this
}

const init = (firstStepperInstance) => {
  pageTitle(window.config.oab.title, '', ' | ')
  that.setHeaderHeight()
  oabSteps = getCollection('.oab-step')
  document.cookie = 'oab-login-redirect-step=false'
  stepObjects = {}
  bindEvents()
  const hashFlag = document.URL.split('#').pop()
  if (hashFlag === "logged") {
    that.oabDataObject = JSON.parse(sessionStorage.getItem('oab-data'))
    that.oabDataObject.wasLogged = true
    window.history.pushState("", document.title, window.location.pathname)
    stepperInstance.nextStep('redirection-step')
    const headerFlag = select('.js-oab-header-flag') ? select('.js-oab-header-flag').innerText : ''
    if (select('[data-default-service]') || that.oabDataObject.isMegaMenuEntryPoint === true) {
      stepStart = headerFlag  === 'true' ? (select('[data-default-service]')) ? 2 : 1 : 0
    }
    else if (select('[data-default-service-plp]') || that.oabDataObject.isServiceEntryPoint === true) {
      if (headerFlag  === 'true')
        stepStart = that.oabDataObject.service.description && that.oabDataObject.service.description.length > 125 ? 4 : (that.oabDataObject.service.extra.mode != null && that.oabDataObject.service.extra.mode.toLowerCase() === 'video') ? 6 : 5
    }
  }
  else if (select('[data-header-flag]')) {
    manageHeaderRefererFlagStepper()
    window.sessionStorage.setItem('isRedirectedToMyAccount', JSON.stringify(false))
  }
  else if (hashFlag === 'backFromLoginPage') {
    that.oabDataObject = JSON.parse(sessionStorage.getItem('oab-data'))
    stepperInstance.nextStep('redirection-step')
    window.sessionStorage.setItem('isRedirectedToMyAccount', JSON.stringify(false))
  }
  else {
    stepperInstance.nextStep('carousel')
    window.sessionStorage.setItem('isRedirectedToMyAccount', JSON.stringify(false))
  }

  window.onzoom = () => { // detect when user zoom browser
    const stepperBackNavigation = select('.js-step-navigation p.container')
    const checkBoxInterests = getCollection('.js-interests-checkbox-input .checkbox')
    const nameFields = getCollection('.js-name-fields .label-input')
    const emailField = select('.js-mail-fields .label-input')
    const countryCodeField = select('.js-input-countryCode .selectbox')
    const phoneNumberField = select('.js-input-phone .js-label-input')
    const commentField = select('.review-message-input')
    const legalMention = select('.js-legal-mention-section p a')
    const devicePixelRatio = window.devicePixelRatio.toFixed(2)
    checkBoxInterests.forEach(checkbox => {
      if (window.devicePixelRatio >= 1 && window.devicePixelRatio < 1.25) {
        checkbox.classList.remove('checkbox-zoomed')
        checkbox.classList.add('checkbox-margin')
      } else {
        checkbox.classList.add('checkbox-zoomed')
        checkbox.classList.remove('checkbox-margin')
      }
      if(navigator.userAgent.indexOf("Firefox") != -1) {
        !isMobileScreen() && devicePixelRatio == 0.60 ? checkbox.classList.add('checkbox-zoomed-firefox') : checkbox.classList.remove('checkbox-zoomed-firefox')
      }
    })
    if (stepperBackNavigation) {
      !isMobileScreen() && window.devicePixelRatio >= 1.25 ? stepperBackNavigation.classList.add('zoomed-navigation') : stepperBackNavigation.classList.remove('zoomed-navigation')
    }
    if (nameFields) {
      nameFields.forEach(nameField => {
        !isMobileScreen() && (devicePixelRatio >= 0.67 && devicePixelRatio <= 0.75) || devicePixelRatio >= 1.50 ? nameField.classList.add('hover-effect') : nameField.classList.remove('hover-effect')
      })
    }

    if (emailField) {
    	!isMobileScreen() && devicePixelRatio == 0.67 ? emailField.classList.add('hover-effect') : emailField.classList.remove('hover-effect')
    }
    if (commentField) {
      !isMobileScreen() && (devicePixelRatio >= 0.67 && devicePixelRatio <= 0.75) ? commentField.classList.add('hover-effect') : commentField.classList.remove('hover-effect')
    }
    if (phoneNumberField) {
    	!isMobileScreen() && (devicePixelRatio >= 0.67 && devicePixelRatio <= 0.80) ? phoneNumberField.classList.add('hover-effect') : phoneNumberField.classList.remove('hover-effect')
    }
    if (legalMention) {
      !isMobileScreen() && (devicePixelRatio >= 0.67 && devicePixelRatio <= 0.80) || devicePixelRatio == 0.33 ? legalMention.classList.add('hover-effect') : legalMention.classList.remove('hover-effect')
      !isMobileScreen() && devicePixelRatio == 0.33 ? legalMention.classList.add('hover-effect-zoom') : legalMention.classList.remove('hover-effect-zoom')
      if(navigator.userAgent.indexOf("Firefox") != -1)
        !isMobileScreen() && devicePixelRatio == 0.50 ? legalMention.classList.add('hover-effect-firefox') : legalMention.classList.remove('hover-effect-firefox')
    }
    if (countryCodeField) {
      !isMobileScreen() && (devicePixelRatio >= 0.67 && devicePixelRatio <= 0.75) || countryCodeField == 0.25 ? countryCodeField.classList.add('hover-effect') : countryCodeField.classList.remove('hover-effect')
    }
  }

  window.onresize = (e) => {
    if(isWideScreen()) 
        that.isZoomed = true

    const event = window.event || e
    if (typeof (window.onzoom) === 'function') {
      return window.onzoom.call(window, event)
    }
  }
}

const getStepIndex = (stepsArray, step) => {
  stepsArray.forEach((oabStep, index) => {
    const stepInfo = oabStep.attributes['data-step'].value
    if (stepInfo === step) {
      that.stepIndex = index
    }
  })
  return that.stepIndex
}

const showStep = (step) => {
  const oabDataStep = oabSteps[step].attributes['data-step'].value
  deleteInstance(stepObjects[oabDataStep])
  oabSteps[step].removeAttribute('hidden')
  resetStepIndex(step)
  switch (oabDataStep) {
    case 'carousel':
      stepContainer = new OabCarousel(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'category':
      if (that.oabDataObject.categoryBypassOnPrevious) {
        delete that.oabDataObject.categoryBypassOnPrevious
        showStep(step - 1)
        return
      }
      else {
        stepContainer = new OabCategory(oabSteps[step], stepperInstance)
        stepObjects[oabDataStep] = stepContainer
        break
      }
    case 'service-list':
      stepContainer = new OabServiceList(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'collection-category':
      stepContainer = new OabCollectionCategory(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'description':
      stepContainer = new OabDescription(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'boutique-selection':
      stepContainer = new OabBoutiqueSelection(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'date-selection':
      stepContainer = new OabDateSelection(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'phone-number':
      stepContainer = new OabPhoneNumber(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'redirection-step':
      stepContainer = new OabRedirectAccount(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    case 'thanks-message':
      stepContainer = new OabThanksMessage(oabSteps[step], stepperInstance)
      stepObjects[oabDataStep] = stepContainer
      break
    default: break
  }
  that.scrollToTop()
}
const prevStep = (stepToGo) => {
  that.oabDataObject.comingFromBack = true
  that.oabDataObject.isBackButtonClicked = true
  const stepToHide = getUpdatedIndex()
  currentStepIndex = getStepIndex(oabSteps, stepToGo)
  const titleToHide = select('.js-step-dynamic-title', oabSteps[stepToHide])
  if (titleToHide) titleToHide.parentNode.removeChild(titleToHide)
  if (currentStepIndex >= 0) {
    oabSteps[stepToHide].setAttribute('hidden', '')
    if (currentStepIndex === stepStart || that.oabDataObject.previousStepToGo[0] == oabSteps[currentStepIndex].attributes['data-step'].value) that.stepperNavigation.setAttribute('hidden', '')
    // get back button analytics from subclasses that don't have access to the element
    if (stepContainer && typeof stepContainer.getBackStepAnalytics === 'function') {
      const analyticsData = stepContainer.getBackStepAnalytics() || {}
      stepperInstance.analyticsHandler('oab-when-user-click-step-back', analyticsData)
    }
    showStep(currentStepIndex)
  }
}

/**
 * If we're coming from Header, we display either categories if > 1 or services
 */
const manageHeaderRefererFlagStepper = () => {
  const dataError = select('[data-error]')
  if (dataError) {
    that.displayErrorModalWindow()
    return
  }

  that.oabDataObject.selectedAxis = window.location.pathname.split('/')[2]
  select('[data-company-id]') && select('[data-company-id]').dataset? that.oabDataObject.companyId = select('[data-company-id]').dataset.companyId : 0
  const defaultServicePLPCompanyId = select('[data-default-service-plp-companyId]')
  that.oabDataObject.companyId = defaultServicePLPCompanyId ? parseInt(defaultServicePLPCompanyId.dataset.defaultServicePlpCompanyid) : that.oabDataObject.companyId
  let selectedAxis
  selectedAxis = that.oabDataObject.selectedAxis.indexOf('watches-jewellery') !== -1 ? 'watches' : that.oabDataObject.selectedAxis
  const axisData = select(`[data-axis-type="${selectedAxis}"]`).dataset
  if (axisData) {
    that.oabDataObject.selectedDivision = axisData.divisionType
    const axisType = axisData.axisType.toLowerCase()
    const axisHeroBanner = select(`.js-axis-hero-banner-${axisType}`)
    const axisHeroDescription = select(`.js-axis-hero-description-${axisType}`)
    if (axisHeroBanner) that.oabDataObject.bannerData.image = axisHeroBanner.innerHTML
    if (axisHeroDescription) that.oabDataObject.bannerData.description = axisHeroDescription.innerHTML
  }
  else {
    that.displayErrorModalWindow()
  }
  const hashFlag = document.URL.split('#').pop()

  const defaultService = select('[data-default-service]')
  const hasServiceList = select('[has-service-list]')
  const defaultServicePLP = select('[data-default-service-plp]')
  const dataDefaultServicePlpDesc = select('[data-default-service-plp-desc]')
  const isServiceEntry = select('[data-default-is-service-entry]')
  const dataDefaultServicePlpFormattedPrice = select('[data-default-service-plp-price-formattedvalue]')
  const dataDefaultServicePlpPriceValue = select('[data-default-service-plp-price-value]')
  const dataIsMegaMenuOnlyEvents = select('[data-only-events]')
  let serviceData
  
  if (defaultServicePLP && dataDefaultServicePlpFormattedPrice && dataDefaultServicePlpPriceValue) {
    const dataService = defaultServicePLP.dataset.defaultServicePlp.split(',')
    that.oabDataObject.service = { serviceId: dataService[0], title: dataService[1], description: dataService[2], extra: {mode: dataService[3]}, minAdvancePeriod: dataService[4], maxAdvancePeriod: dataService[5], questionSetId: dataService[6], price: {formattedValue: dataService[7], value: dataService[8]}, duration: {duration: dataService[9], unit: {code: dataService[10]}}, associatedEvents: dataService[11].trim() === "true" ? true : false, eventGroupName : null}
    that.oabDataObject.service.eventGroupName = that.oabDataObject.service.associatedEvents ? dataService[12].trim() : ''
    that.oabDataObject.service.price.formattedValue = dataDefaultServicePlpFormattedPrice.dataset.defaultServicePlpPriceFormattedvalue
    that.oabDataObject.service.price.value = dataDefaultServicePlpPriceValue.dataset.defaultServicePlpPriceValue
    that.oabDataObject.service.description = dataDefaultServicePlpDesc ? dataDefaultServicePlpDesc.dataset.defaultServicePlpDesc : ''
    that.oabDataObject.isServiceEntryPoint = true
    setQuestionData()
  }

  if (defaultService || hasServiceList) {
    stepStart = 2
    if (hashFlag === 'backFromLoginPage') {
      that.oabDataObject = JSON.parse(sessionStorage.getItem('oab-data'))
      stepperInstance.nextStep('redirection-step')
    } else {
      stepperInstance.nextStep('service-list')
    }
    that.oabDataObject.isMegaMenuEntryPoint === true
  }
  else if (defaultServicePLP || isServiceEntry.dataset.defaultIsServiceEntry == "true") {
    serviceData = that.oabDataObject.service ? that.oabDataObject.service : JSON.parse(sessionStorage.getItem('oab-data')).service
    stepStart = serviceData.description && serviceData.description.length > 125 ? 4 : (serviceData.extra.mode != null && serviceData.extra.mode.toLowerCase() === 'video') ? 6 : 5
    if (hashFlag === 'backFromLoginPage') {
      that.oabDataObject = JSON.parse(sessionStorage.getItem('oab-data'))
      stepperInstance.nextStep('redirection-step')
    } else {
      if (serviceData.description && serviceData.description.length > 125) { // if the selected service contains a description and description has more than 125 characters then go to description step
        stepperInstance.nextStep('description')
      } else {
        if (serviceData.extra.mode != null && serviceData.extra.mode.toLowerCase() === 'video') {
          stepperInstance.nextStep('date-selection') // go to date selection if user has selected virtual service
        } else {
          stepperInstance.nextStep('boutique-selection') // else go directly to boutique selection step
        }
      }
    }
  }
  else if (dataIsMegaMenuOnlyEvents && dataIsMegaMenuOnlyEvents.dataset.onlyEvents === "true") {
    stepStart = 2
    stepperInstance.nextStep('service-list')
  }
  else {
    stepStart = 1
    if (hashFlag === 'backFromLoginPage') {
      that.oabDataObject = JSON.parse(sessionStorage.getItem('oab-data'))
      stepperInstance.nextStep('redirection-step')
    } else {
      stepperInstance.nextStep('category')
    }
  }
}

const setQuestionData = () => {
  if (parseInt(that.oabDataObject.service.questionSetId) !== 0) {
    const collectionCategoryParams = {
      questionSetId: that.oabDataObject.service.questionSetId,
      companyId: that.oabDataObject.companyId
    }
    // call categories and collection webservice to check if question is a comment or a category & collection
    OAB_API_MANAGER.getCategoryCollectionList(collectionCategoryParams)
      .then((data) => {
        that.oabDataObject.questionListData = data
        that.questionList = data.questionList
        if (that.questionList) {
          that.questionList.forEach((question) => {
            that.oabDataObject.interests = question.questionName.toLowerCase() === 'customer_interests'
          })
        } else if (data.errors) { // if an error occurs while calling collection and categories webservice then show the error notifier
          notifier.show({
            name: window.config.locales.notifier.request.label,
            message: window.config.locales.notifier.request.message,
            close: window.config.locales.notifier.request.close,
          })
        }
      })
  } 
}


const resetStepIndex = (index) => {
  for (let i = 0; i < oabSteps.length; i++) {
    oabSteps[i].classList.remove('active')
  }
  oabSteps[index].classList.add('active')
}
const getUpdatedIndex = () => {
  currentStep = select('.oab-step.active')
  currentStepIndex = oabSteps.indexOf(currentStep)
  return currentStepIndex
}
const bindEvents = () => {
  const prev = select('.js-prev-button')

  prev.addEventListener('click', () => {
    const stepTogetBackTo = that.oabDataObject.previousStepToGo
    if((that.stepIndex == 1 || that.stepIndex == 2) && that.oabDataObject.previousStepToGo[0] != oabSteps[that.stepIndex].attributes['data-step'].value){ 
      that.setWhiteBanner()
   }
    stepTogetBackTo.pop()
    prevStep(stepTogetBackTo[stepTogetBackTo.length - 1])
  })

  window.addEventListener('scroll',(event) => {
    isIosTab()
    if(that.isZoomed) {
      that.visibleHeader()
    }

    if(that.oabDataObject.previousStepToGo[0] != oabSteps[that.stepIndex].attributes['data-step'].value) 
          isTabLandscapeView()

    if((that.stepIndex == 1 || that.stepIndex == 2) && that.oabDataObject.previousStepToGo[0] != oabSteps[that.stepIndex].attributes['data-step'].value){
      let bnrImage = select('.js-axis-banner img', oabSteps[that.stepIndex])
      if(!isMobileScreen() && that.oabDataObject.bannerData && that.oabDataObject.bannerData.image && bnrImage != null) {
        let selectedList = that.stepIndex == 1 ? select('.oab-category-list') : select('.oab-service-list')
        that.stickyPosition = that.stickyPosition == 0 ? selectedList.offsetTop : that.stickyPosition
        if (window.pageYOffset >= that.stickyPosition) { 
          that.stepNavigation.removeAttribute('hidden')
      } else {
          that.stepNavigation.setAttribute('hidden', '')
        }
      }
    }
  })
}

const isIosTab = () => {
  if ( isSafari() && (screen.width > 600 && screen.width < 1200) ) {
    let isIosStep =  select('.js-step-navigation .is-fixed')
    if (isIosStep != null) {
      let isIosStepStyles = window.getComputedStyle(isIosStep)
      if(isIosStepStyles.zIndex == 0)
          isIosStep.style.zIndex = 60
    }
  }
}

const isTabLandscapeView = () => {
  let narrowHeader = select('.js-header.is-narrow')
  if(narrowHeader && !isMobileScreen() && !isTabletScreen()) {
    let footerElement = select('.subfooter')
    let headerStep = select('.js-step-navigation div')
      if(footerElement && isInViewPort(footerElement, footerElement.clientHeight))  {
          if(headerStep) {
            headerStep.classList.remove('is-fixed')
            headerStep.classList.add('is-static')
          }
      } else {
        if(headerStep) {
          headerStep.classList.add('is-fixed')
          headerStep.classList.remove('is-static')
        }
      }
  }
}

const deleteInstance = (instance) => {
  if (instance) {
    instance.destroy()
    instance = null
  }
}

export default function (el) {
  stepperInstance = new Stepper(el)
  init(stepperInstance)
  return stepperInstance
}
