import Base from 'modules/base'
import bind from 'delegate'
import { select, selectAll, getCollection, closest } from 'lib/selectors'
import { focusElement, isMobileScreen } from 'lib/helpers'
import OAB_API_MANAGER from './oab-api-manager'
import pageTitle from 'modules/pageTitle'

const CAROUSEL_BUTTON = '.js-axis-carousel-btn'
const CAROUSEL_PREV_BTN = '.js-carousel-prev'
const CAROUSEL_NEXT_BTN = '.js-carousel-next'
const CAROUSEL_TITLE = '.js-carousel-title'
const LOCALIZED_AXIS_NAME = '.js-localised-axis-type'
const STEP_TITLE = '.js-carousel-title'
const COMPONENT_TITLE = '.js-carousel-component-title'
const COMPONENT_LEVEL = '.js-carousel-component-titleLevelGlobal'
const COMPONENT_SIZE = '.js-carousel-component-titleSizeGlobal'
const STEP_NAVIGATION = '.js-step-navigation'
const OAB_CONTAINER = '.js-oab-container'

class OabCarousel extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.selectedAxis = this.master.oabDataObject.selectedAxis
    const oabContainer = closest(this.el, OAB_CONTAINER)
    if (oabContainer && isMobileScreen()) oabContainer.classList.toggle('container', true)
    this.events = []
    this.init()
    this.bindEvents()
  }

  init() {
    pageTitle(window.config.oab.title, '', ' | ')
    this.stepTitle = select(STEP_TITLE, this.el)
    this.componentTitle = select(COMPONENT_TITLE, this.el).innerHTML
    this.componentLevel = select(COMPONENT_LEVEL, this.el).innerHTML
    this.componentSize = select(COMPONENT_SIZE, this.el).innerHTML
    this.stepperNavigation = select(STEP_NAVIGATION)
    this.stepperNavigation.setAttribute('hidden', '')
     this.master.destroyLoader()
    if (this.master.firstInit) {
      this.stepTitle.innerHTML = ''
      const title = this.master.createStepTitle(this.componentTitle, 'lblBookAnAppointment', this.componentLevel, this.componentSize.substring(1))
      this.stepTitle.classList.add('element-centered')
      this.stepTitle.appendChild(title)
    }

    this.axisButtons = getCollection(CAROUSEL_BUTTON, this.el)
    this.swipeLeft = select('.js-carousel-prev')
    this.swipeRight = select('.js-carousel-next')
    this.carouselTitle = select(CAROUSEL_TITLE)
    const carouselH1Element = select('.js-step-dynamic-title', this.carouselTitle)
    if (carouselH1Element && this.master.oabDataObject.comingFromBack === true) focusElement(carouselH1Element)
    let axisButtons = selectAll('.js-axis-carousel-btn')
    let widths = axisButtons.map(button => button.offsetWidth)
    let paddingValue = 20
    let maxWidthButton = Math.max.apply(null, widths) + paddingValue
    axisButtons.forEach((item) => {
      item.style.width = `${maxWidthButton}px`
    })
  }

  bindEvents() {
    const analyticsData = {
      eventCategory: 'book an appointment',
      eventAction: 'home'
    }
    this.events.push(bind(this.el, CAROUSEL_PREV_BTN, 'click', () => {
      this.analyticsHandler('when-user-swipe', analyticsData)
    }))
    this.events.push(bind(this.el, CAROUSEL_NEXT_BTN, 'click', () => {
      this.analyticsHandler('when-user-swipe', analyticsData)
    }))

    this.events.push(bind(this.el, CAROUSEL_BUTTON, 'click', (evt) => {
      this.selectAxis(evt.delegateTarget)
    }))
  }

  selectAxis = (e) => {
    this.master.showLoader(e, true)
    const axisType = e.attributes['data-axis-type'].value.toLowerCase()
    const oabProvider = select('.js-oab-provider')
    const divisionType = e.attributes['data-division-type'].value.toLowerCase()
    this.master.oabDataObject.selectedAxis = axisType
    this.master.oabDataObject.selectedDivision = divisionType
    const axisHeroBanner = select(`.js-axis-hero-banner-${axisType}`, this.el)
    const axisHeroDescription = select(`.js-axis-hero-description-${axisType}`, this.el)
    if (axisHeroBanner) this.master.oabDataObject.bannerData.image = axisHeroBanner.innerHTML
    if (axisHeroDescription) this.master.oabDataObject.bannerData.description = axisHeroDescription.innerHTML
    const analyticsData = {
      eventCategory: 'book an appointment',
      eventAction: axisType
    }
     this.master.destroyLoader()
    this.analyticsHandler('when-user-click-on-axis-button', analyticsData)
	if(oabProvider.innerText === 'JRNI'){
	OAB_API_MANAGER.getCategoryList({ axisType, divisionType }, [    response => {
        while (this.stepTitle.firstChild) {
          this.stepTitle.removeChild(this.stepTitle.firstChild)
        }
        this.master.destroyLoader()
        if (response.categoryList.length === 1) {
          this.master.oabDataObject.companyId = response.companyId
          this.master.oabDataObject.category = {
            categoryId: response.categoryList[0].categoryId,
            categoryName: response.categoryList[0].name
          }
          this.master.nextStep('service-list')
        } else if (response.categoryList.length > 1) {
          this.master.oabDataObject.categoryListObject = response
          this.master.nextStep('category')
        } else {
          this.master.oabDataObject.companyId = response.companyId
          this.master.nextStep('service-list')
        }
      },
      response => {
        this.master.destroyLoader()
        this.master.displayErrorModalWindow()
      }, null
    ])
  }else{
        this.master.nextStep('service-list')
     }
    this.master.firstInit = true
  }

  analyticsHandler = (action, object) => {
    window.document.DLKit.push(action, object)
  }

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
  }
}

export default OabCarousel
