import Base from 'modules/base'
import { getCollection, select, selectAll, closest } from 'lib/selectors'
import bind from 'delegate'
import OAB_API_MANAGER from './oab-api-manager'
import { focusElement, isMobileScreen, isInViewPort } from 'lib/helpers'
import pageTitle from 'modules/pageTitle'
import flatpickr from "flatpickr"

// selectors
const LABEL_INPUT = '.js-label-input'
const INPUT_FIELD = '.js-select-date-input'
const SUBMIT_BTN = '.js-date-select-submit'
const EDIT_BTN = '.js-calendar-button'
const SELECT_DATE_FORM = '.js-select-date-form'
const DATE_CONTAINER = '.js-date-container'
const LIST_TITLE = '.js-slot-title'
const CONFIRM_SELECTION_BUTTON = '.js-confirm-selection'
const CONFIRM_SELECTION_PARA = '.js-date-cta-container'
const DATE_SELECTION_TITLE = '.js-date-selection-title'
const DATE_SELECTION_RADIO = '.oab-date-item [type="radio"]'
const CALENDAR_YEAR_INPUT = '.flatpickr-current-month > div input'
const COUNTRY_SELECTED = config.currentLanguage.split('_')[0]
const CALENDAR_DATE_LABEL = '.oab-visible-submit > div label'
const oabProvider = select('.js-oab-provider')
class OabDateSelection extends Base {
  constructor(el, master) {
    super(el)
    this.el = el
    this.master = master
    this.stepName = this.el.attributes['data-step'].value
    this.stepsArray = this.master.oabDataObject.previousStepToGo
    if (!this.stepsArray.includes(this.stepName)) {
      this.stepsArray.push(this.stepName)
    }
    this.companyId = this.master.oabDataObject.companyId
    this.serviceId = this.master.oabDataObject.service.serviceId
    this.selectedServiceTitle = this.master.oabDataObject.service.title
    this.minAdvancePeriod = this.master.oabDataObject.service.minAdvancePeriod
    this.maxAdvancePeriod = this.master.oabDataObject.service.maxAdvancePeriod
    this.selectedBoutique = this.master.oabDataObject.selectedBoutique
    this.events = []
    this.selectedAxis = this.master.oabDataObject.selectedAxis
    this.selectedDateObject = this.master.oabDataObject.selectedDateSlot
    this.isAtDateLinePosition = 0

    this.init()
    if(config.currentLanguage.split('_')[0] === 'vi') {
       import(`flatpickr/dist/l10n/${config.currentLanguage.toLowerCase().split('_')[1]}.js`)
        .then(() => {
          flatpickr.localize(flatpickr.l10ns[config.currentLanguage.toLowerCase().split('_')[1]])
          this.bindEvents()
          console.log(`${config.currentLanguage.split('_')[0]} calendar translations loaded`)
        })
        .catch(err => {
          console.log(err)
        })
    }
	else if(config.currentLanguage.split('_')[0] !== 'en'){
		 import(`flatpickr/dist/l10n/${config.currentLanguage.split('_')[0]}.js`)

		   .then(() => {
          flatpickr.localize(flatpickr.l10ns[COUNTRY_SELECTED])
          this.bindEvents()
          console.log(`${config.currentLanguage.split('_')[0]} calendar translations loaded`)
        })
        .catch(err => {
          console.log(err)
        })
	}
	else {
      this.bindEvents()
    }
    const oabContainer = closest(this.el, '.js-oab-container')
    if (oabContainer && isMobileScreen()) oabContainer.classList.toggle('container', true)
  }

  init() {
    this.availableSlots = this.master.oabDataObject.availableSlots || null
    this.labelInput = select(LABEL_INPUT, this.el)
    this.inputField = select(INPUT_FIELD, this.el)
    this.submitButton = select(SUBMIT_BTN, this.el)
    this.editButton = select(EDIT_BTN, this.el)
    this.calendarDateLabel = select(CALENDAR_DATE_LABEL, this.el)
    this.selectDateForm = select(SELECT_DATE_FORM, this.el)
    this.dateContainer = select(DATE_CONTAINER, this.el)
    this.listTitleContainer = select(LIST_TITLE, this.el)
    this.titleContainer = select(DATE_SELECTION_TITLE)
    this.selectConfirmationButton = select(CONFIRM_SELECTION_BUTTON, this.el)
    this.selectConfirmationPara = select(CONFIRM_SELECTION_PARA, this.el)
    this.stepTitle = window.config.oab.dateSelection.title
    const titleElement = this.master.createStepTitle(this.stepTitle, 'lblDateSelection', 'h1', '2')
    this.titleContainer.insertBefore(titleElement, this.titleContainer.firstChild)
    this.calendarContainer = select('.calendar-container')
    if (this.selectConfirmationButton) this.selectConfirmationButton.setAttribute('hidden', '')
    if (this.dateContainer) {
      this.dateContainer.innerHTML = ''
    }

    pageTitle(this.stepTitle, '', ' - ')
    this.cleanErrors(this.selectDateForm)
    this.master.showLoader(this.el, false, 0, () => {
      const loader = select('.loader', this.el)
      loader.style.width = '100%'
      const stepTitle = select('.js-step-dynamic-title', this.titleContainer)
      if (stepTitle) focusElement(this.titleContainer.firstChild, { notChangeTabIndex: true })
      this.inputField.removeAttribute('tabindex')
    })
    this.master.setHeaderHeight()
  }

  bindEvents() {
    this.CalendarInstance = flatpickr('.oab-visible-submit', {
      dateFormat: config.country === 'JP' ? 'Y/m/d' : 'd/m/Y',
      minDate: 'today',
      clickOpens: false,
      allowInput: true,
      disableMobile: true,
      appendTo: this.calendarContainer,
      wrap: true,
      locale: {
        firstDayOfWeek: 1
      },
      disable: [
        function(date) {
          return (true)
        }
      ],
      onOpen: () => {
        if (this.calendarContainer) {
          this.calendarContainer.removeAttribute('hidden')
          this.submitButton.removeAttribute('hidden')
          this.editButton.setAttribute('hidden', '')
          this.inputField.removeAttribute('style')
          this.inputField.focus()
          if(isMobileScreen() && config.country !== 'JP' && this.isAtDateLinePosition == 0) {
            this.inputField.setSelectionRange(2,2)
            this.isAtDateLinePosition++
          }
        }
      },
      onClose: () => {
        if (this.calendarContainer) {
          this.calendarContainer.setAttribute('hidden', '')
          this.submitButton.setAttribute('hidden', '')
          this.editButton.removeAttribute('hidden')
          if(isMobileScreen() && document.activeElement != this.inputField) this.isAtDateLinePosition = 0
        }
      },
      onReady: () => {
        select(CALENDAR_YEAR_INPUT).maxLength = 4
        if (this.master.oabDataObject.service != null && this.master.oabDataObject.service.extra.mode.toLowerCase() === 'video') {
          this.getBoutiqueDataVirtualService()
        }
        else {
          this.callDateAPI()
        }
      },
      onChange: () => {
        if (!this.master.loaderExist) {
          this.master.showLoader(this.el, false, 0, () => {
            const loader = select('.loader', this.el)
            loader.style.width = '100%'
          })
        }
      },
      onMonthChange: () => {
        this.addBackground()
      }
    })

    // Override the keydown eventlistener of the library that empty the field
    // on key "enter"
    const elClone = this.inputField.cloneNode(true)
    this.inputField.parentNode.replaceChild(elClone, this.inputField)
    this.inputField = select(INPUT_FIELD, this.el)
    this.calendarYearInputField = select(CALENDAR_YEAR_INPUT, this.el)

    this.events.push(bind(this.el, CONFIRM_SELECTION_BUTTON, 'click', () => this.selectDateSlot()))
    this.events.push(bind(this.el, INPUT_FIELD, 'input', (event) => this.onInput(this.inputField, event)))
    this.events.push(bind(this.el, INPUT_FIELD, 'keydown', (event) => this.onKeydown(event)))
    this.events.push(bind(this.el, CALENDAR_YEAR_INPUT, 'input', () => this.onCalenderYearInput(this.calendarYearInputField)))
    this.events.push(bind(this.el, INPUT_FIELD, 'click', (event) => {
      if(isMobileScreen() && config.country !== 'JP' && this.isAtDateLinePosition == 0) {
        this.inputField.setSelectionRange(2,2)
        this.isAtDateLinePosition++
      }
      this.submitButton.removeAttribute('hidden')
      this.editButton.setAttribute('hidden', '')
      this.inputField.removeAttribute('style')
    }))

    this.events.push(bind(this.el, INPUT_FIELD, 'focusout', (event) => {

      if (this.inputField.value.length === 0) {
        this.labelInput.classList.remove('has-content')
      }
      if(isMobileScreen()) this.isAtDateLinePosition = 0
      this.CalendarInstance.close()
    }))

    this.events.push(bind(this.el, SUBMIT_BTN, 'mousedown', (e) => {
      this.submitButton.click()
    }))

    this.events.push(bind(this.el, SUBMIT_BTN, 'click', (e) => {
      const currentLoader = select('.loader__content')
      if (currentLoader === null) {
        this.master.showLoader(this.el, false, 0, () => {
          const loader = select('.loader', this.el)
          loader.style.width = '100%'
        })
      }
      this.getAvailableDates(e)
      this.inputField.setAttribute('style', 'caret-color: transparent;')
      this.inputField.removeAttribute('tabindex')
    }))

    // Workaround on Safari not triggering focusin on label clicks
    this.events.push(bind(this.el, '.oab-date-item', 'click', (evt) => {
      const input = select('input', evt.delegateTarget)
      if (input) input.focus()
      }
    ))
    this.events.push(bind(this.el, DATE_SELECTION_RADIO, 'focusin', (evt) => this.focusAndSelectToggle(evt)))
    this.events.push(bind(this.el, DATE_SELECTION_RADIO, 'focusout', (evt) => this.focusOutCallback(evt)))
  }

  getBoutiqueDataVirtualService() {
    let that = this
    let url = that.master.oabDataObject.service.associatedEvents ? OAB_API_MANAGER.getAvailableBoutiquesByEvent : OAB_API_MANAGER.getAvailableBoutiquesByService
    let initializationRequestParam
    if(this.master.oabDataObject.service.associatedEvents) {
      initializationRequestParam = {
        companyId: this.companyId,
        eventName: this.master.oabDataObject.service.title,
        eventGroupName:  this.master.oabDataObject.service.eventGroupName
      }
    } else {
        initializationRequestParam = {
          companyId: this.companyId,
          serviceId: this.master.oabDataObject.service.serviceId
        }
      }
    url(initializationRequestParam, [response => this.initBoutiqueListCallback(response)])
  }

  initBoutiqueListCallback(response) {
    if (response.errors && response.errors.error) {
      this.master.displayErrorModalWindow()
      return
    }
    this.bindBoutiqueListData(response)
  }
  bindBoutiqueListData(data){
    this.master.oabDataObject.boutiques = data.boutiques
    this.callDateAPI()
  }

  callDateAPI() {
    if (this.master.oabDataObject.service.extra.mode != null && this.master.oabDataObject.service.extra.mode.toLowerCase() === 'video') {
      const serviceListIDArr = this.master.oabDataObject.boutiques.filter(item => item.id !== null)
      .map(item => item.id)
      const selectedBoutiqueId = serviceListIDArr[Math.floor(Math.random() * serviceListIDArr.length)]
      const selectedBoutique = this.master.oabDataObject.boutiques.filter(item => item.id === selectedBoutiqueId)
      this.master.oabDataObject.selectedBoutique = selectedBoutique[0]
      this.selectedBoutique = selectedBoutique[0]
    }
    let initializationRequestParam
    if(this.master.oabDataObject.service.associatedEvents) {
      initializationRequestParam = {
        serviceId: this.selectedBoutique.eventId,
        boutiqueId: this.selectedBoutique.id,
        startDate: this.selectedBoutique.startDate,
        endDate: this.selectedBoutique.endDate,
        eventGroupId: this.selectedBoutique.eventGroupId
      }
    } else {
     if(oabProvider.innerText === "SALESFORCE"){
      initializationRequestParam = {
		 serviceType: this.master.oabDataObject.service.salesforceId,
		 sfBoutiqueId: this.selectedBoutique.sfBoutiqueId
		}
     } else {
      initializationRequestParam = {
        serviceId: this.serviceId,
        boutiqueId: this.selectedBoutique.id,
        minAdvance: this.minAdvancePeriod,
        maxAdvance: this.maxAdvancePeriod
      }
     }
  }

 if(this.master.oabDataObject.service.associatedEvents) {
   OAB_API_MANAGER.getEventDateAndTime(initializationRequestParam, [
      response => {
        this.handleDateTimeResponse(response)
      },
      response => this.master.displayErrorModalWindow()
    ])
  } else {
      OAB_API_MANAGER.getDateAndTime(initializationRequestParam, [
        response => {
          this.handleDateTimeResponse(response)
        },
        response => this.master.displayErrorModalWindow()
      ])
    }
  }

  handleDateTimeResponse(response) {
    if (response.errors && response.errors.error && config.region !== 'apac') {
      this.master.displayErrorModalWindow()
      return
    }
    else if (response.availableDateTimeList && response.availableDateTimeList.length > 0) {
      this.CalendarInstance.set('minDate', new Date(response.availableDateTimeList[0].time))
      this.CalendarInstance.set('enable', response.availableDates.map((item)=> new Date(item)))
      this.CalendarInstance.set('onValueUpdate', (selectedDates, dateStr, instance) => {
        if (instance.isOpen) {
          this.inputField.value = dateStr
          this.getAvailableDates()
        }
      })
    }
    this.dateSelectionCallback(response, null)
    if (this.selectedDateObject && this.master.oabDataObject.comingFromBack) {
      const availableDate = new Date(this.selectedDateObject.date.time.substring(0, this.selectedDateObject.date.time.length - 6))
      this.inputField.value = OabDateSelection.getInputValueDate(availableDate)
      this.submitButton.click()
    }
  }

  getAvailableDates(e) {
    if (e) e.preventDefault()
    this.dateContainer.innerHTML = ''
    this.listTitleContainer.setAttribute('hidden', '')
    const inputValue = this.inputField.value.split('/').map((v) => parseInt(v.replace(/\D/g, '')))
    const isDateValid = config.country === 'JP' ? this.isValidDate(inputValue[0], (inputValue[1] - 1), inputValue[2]) : this.isValidDate(inputValue[2], (inputValue[1] - 1), inputValue[0])
    if (isDateValid === true && (config.country !== 'JP' && this.inputField.value.match(/^\d{1,2}\/\d{1,2}\/\d{4}$/) || (config.country === 'JP' && this.inputField.value.match(/^\d{4}\/\d{1,2}\/\d{1,2}$/)))) {
      this.cleanErrors(this.selectDateForm)
      let dateSelectedRequestParam
      if (this.master.oabDataObject.service.associatedEvents) {
        dateSelectedRequestParam = {
          selectedDate: this.inputField.value.split('/').reverse().join('/'),
          serviceId: this.selectedBoutique.eventId,
          boutiqueId: this.selectedBoutique.id,
          startDate: this.selectedBoutique.startDate,
          endDate: this.selectedBoutique.endDate,
          eventGroupId: this.selectedBoutique.eventGroupId
        }
      } else {
        if(oabProvider.innerText === "SALESFORCE"){
         dateSelectedRequestParam = {
          selectedDate: this.inputField.value.split('/').reverse().join('/'),
          serviceType: this.master.oabDataObject.service.salesforceId,
          sfBoutiqueId: this.selectedBoutique.sfBoutiqueId
        }
        } else {
        dateSelectedRequestParam = {
          selectedDate: this.inputField.value.split('/').reverse().join('/'),
          serviceId: this.serviceId,
          boutiqueId: this.selectedBoutique.id,
          minAdvance: this.minAdvancePeriod,
          maxAdvance: this.maxAdvancePeriod
        }
      }
      }
      if (this.master.oabDataObject.service.associatedEvents) {
        OAB_API_MANAGER.getEventDateAndTime(dateSelectedRequestParam, [response => this.dateSelectionCallback(response, dateSelectedRequestParam.selectedDate), response => this.master.displayShowErrorMessage(response)])
      } else {
        OAB_API_MANAGER.getDateAndTime(dateSelectedRequestParam, [response => this.dateSelectionCallback(response, dateSelectedRequestParam.selectedDate), response => this.master.displayShowErrorMessage(response)])
      }
    } else {
      this.cleanErrors(this.selectDateForm)
      if ((config.country !== 'JP' && !this.inputField.value.match(/^\d{1,2}\/\d{1,2}\/\d{4}$/)) || (config.country === 'JP' && !this.inputField.value.match(/^\d{4}\/\d{1,2}\/\d{1,2}$/))) {
        this.createErrorMessage(this.labelInput.parentNode, window.config.oab.dateSelection.errors.invalidDate)
      } else if (isDateValid !== true && this.availableSlots !== null) {
        this.createErrorMessage(this.labelInput.parentNode, window.config.oab.dateSelection.errors.unavailableDate)
      } else if (oabProvider.innerText === 'SALESFORCE' && isDateValid === 'past' && this.availableSlots === null) {
        this.createErrorMessage(this.labelInput.parentNode, window.config.oab.dateSelection.errors.unavailableServiceDate)
      }
      this.submitButton.setAttribute('hidden', '')
      this.editButton.removeAttribute('hidden')
      if (this.calendarContainer) this.calendarContainer.setAttribute('hidden', '')
      if (this.master.loaderExist) this.master.destroyLoader()
    }
  }

  addBackground() {
    let isDateSelected = select('span.flatpickr-day.selected')
    let firstAvailableDate = select('span.flatpickr-day:not(.disabled)')
    isDateSelected ? window.sessionStorage.setItem('selectedSpanAriaLabel', isDateSelected.ariaLabel) :  ''
    let allDates = selectAll('span.flatpickr-day')
    allDates.forEach((date) => {
      if(date.ariaLabel === window.sessionStorage.getItem('selectedSpanAriaLabel')) {
        date.classList.add('selected')
        isDateSelected = date
      }
    })
    if(isDateSelected == null && window.sessionStorage.getItem('selectedSpanAriaLabel') === '' &&  firstAvailableDate !== null) {
      firstAvailableDate.classList.add('selected')
      window.sessionStorage.setItem('selectedSpanAriaLabel', firstAvailableDate.ariaLabel)
    }
  }

  dateSelectionCallback(response, selectedDate) {
    if (response.errors && response.errors.error && config.region !== 'apac') {
      this.master.displayErrorModalWindow()
      return
    }
    this.dateContainer.innerHTML = ''
    this.cleanErrors(this.selectDateForm)
    if (response.availableDateTimeList && response.availableDateTimeList.length == 0) {
      this.listTitleContainer.setAttribute('hidden', '')
    }
    if (response.availableDateTimeList && response.availableDateTimeList.length > 0) {
      this.availableSlots = this.master.oabDataObject.availableSlots = response.availableDateTimeList
      response.availableDateTimeList.forEach((date, index) => {
        var dateArray = date.time.split(/[^0-9]/)
        const availableDate = new Date(dateArray[0], dateArray[1] - 1, dateArray[2], dateArray[3], dateArray[4], dateArray[5])
        const availableDateDuration = new Date (availableDate.getTime() + this.master.oabDataObject.service.duration.duration*60000)

        // date slot template
        const timeTemplate = `<li data-time-id="${date.timeId}" data-person="${date.adviserId}" data-test="btnTimeSlot" class="oab-date-item ${(isMobileScreen()) ? 'label-item' : `label-item-${OabDateSelection.isPair(index)}`}">
                              <input name="date_radio_input" id="date_radio_${index}" value="${availableDate}" type="radio" aria-required="true" class="is-sr-only" ${index === 0 ? 'checked' : ''}>

                              <label class="date-radio-label ${(isMobileScreen()) ? '' : `label-item-${OabDateSelection.isPair(index)}`}" for="date_radio_${index}">
                              <span>` + window.config.oab.dateSelection.timeSlotFromText + ' ' + `${availableDate.toLocaleTimeString([window.config.currentLanguage.replace('_', '-')], {hourCycle: 'h23', hour: '2-digit', minute: '2-digit'})}` + ' ' + window.config.oab.dateSelection.timeSlotToText + ' ' + `${availableDateDuration.toLocaleTimeString([window.config.currentLanguage.replace('_', '-')], { hourCycle: 'h23', hour: '2-digit', minute: '2-digit'})}</span></label>


							                </li>`
        this.dateContainer.innerHTML += timeTemplate
        this.labelInput.classList.add('has-content')
        this.inputField.value = OabDateSelection.getInputValueDate(availableDate)
        this.toggleClearSubmit()
      })

      this.slotList = getCollection(DATE_SELECTION_RADIO, this.dateContainer)
      const firstRadio = select('input', this.dateContainer.firstChild)
      if (!this.master.oabDataObject.comingFromBack && selectedDate !== null) firstRadio.focus()
      if (this.master.oabDataObject.selectedDateSlot && this.master.oabDataObject.comingFromBack) {
        const selectedSlotIndex = this.master.oabDataObject.selectedDateSlot.slotIndex
        const selectedSlot = this.slotList[selectedSlotIndex]
        this.slotList.forEach((element) => {
          if (element === selectedSlot  && this.master.oabDataObject.availableSlots[selectedSlotIndex] && this.master.oabDataObject.availableSlots[selectedSlotIndex].time === this.master.oabDataObject.selectedDateSlot.date.time) {
            element.parentElement.classList.toggle('is-focused', true)
            element.checked = true
            this.selectedDateSlot = {
              slotIndex: this.slotList.indexOf(element),
              date: this.availableSlots[this.slotList.indexOf(element)]
            }
          }
        })

        setTimeout(() => {
        this.selectedSlotItem = select(`${DATE_CONTAINER} .is-focused`)
        if(this.selectedSlotItem) {
            const sellectedInput = select('input', this.selectedSlotItem)
            if (sellectedInput) sellectedInput.focus()
          }
        }, 1000);
      }
      this.selectConfirmationButton.removeAttribute('hidden')
      this.addBackground()
    } else if (response.availableDateTimeList && response.availableDateTimeList.length === 0 && !selectedDate) {
      this.createErrorMessage(this.labelInput.parentNode, window.config.oab.dateSelection.errors.unavailableServiceDate)
    } else if(config.region !== 'apac' || response.availableDateTimeList != null){
      this.createErrorMessage(this.labelInput.parentNode, window.config.oab.dateSelection.errors.unavailableDate)
    }else {
      this.createErrorMessage(this.labelInput.parentNode, window.config.oab.dateSelection.errors.unavailableServiceDate)
    }
    this.master.destroyLoader(() => {
      TweenLite.set(this.el, {
        delay: 0.3,
        onComplete: () => {
          if (response.availableDateTimeList && response.availableDateTimeList.length > 0) {
            this.dateContainer.removeAttribute('hidden')
            this.listTitleContainer.removeAttribute('hidden', '')
          }
        }
      })
    })
  }

  static isPair(index) {
    if ((index % 3) === 0) {
      return 'pair'
    }
    return 'middle'
  }

  static getInputValueDate(dateString) {
    const newDate = new Date(dateString)
    const month = (`0${(newDate.getMonth() + 1)}`).slice(-2)
    const day = (`0${newDate.getDate()}`).slice(-2)
    const year = newDate.getFullYear()
    const date = (config.country === 'JP') ? `${year}/${month}/${day}` : `${day}/${month}/${year}`
    return config.country === 'JP' ? `${year}/${month}/${day}` : `${day}/${month}/${year}`
  }

  toggleClearSubmit() {
    const isFilled = this.inputField.value.length > 0 ? 1 : 0
    if (isFilled > 0) {
      this.submitButton.removeAttribute('disabled')
      this.submitButton.setAttribute('aria-disabled', false)
      this.calendarDateLabel.classList.remove('no-pointer')
    } else {
      this.submitButton.setAttribute('disabled', 'disabled')
      this.submitButton.setAttribute('aria-disabled', true)
      this.calendarDateLabel.classList.add('no-pointer')
    }
  }

  clearText() {
    this.labelInput.classList.remove('has-content')
    this.inputField.value = ''
    this.dateContainer.innerHTML = ''
    this.selectConfirmationButton.setAttribute('hidden', '')
    this.inputField.focus()
    this.cleanErrors(this.selectDateForm)
  }

  onCalenderYearInput(e) {
    const inputYear = e.value.replace(/[^0-9]/g, '');
    e.value = inputYear
  }

  onInput(dateInputSelector, event) {
    const isInputCharacter = event.inputType === 'deleteContentForward' || event.inputType === 'deleteContentBackward'
    if (config.country !== 'JP' && !isInputCharacter && (dateInputSelector.value.match(/^\d{2}$/) || dateInputSelector.value.match(/^\d{2}\/\d{2}$/))) {
      dateInputSelector.value += '/'
    } else if (config.country === 'JP' && !isInputCharacter && (dateInputSelector.value.match(/^\d{4}$/) || dateInputSelector.value.match(/^\d{4}\/\d{2}$/))){
      dateInputSelector.value += '/'
    }
    const startPos = dateInputSelector.selectionStart
    let cursorPosition = startPos
    dateInputSelector.classList.toggle('is-filled', dateInputSelector.value.length > 0)
    const regex = /^[0-9/]/
    const values = dateInputSelector.value.split().map((v) => {
      if (!regex.test(dateInputSelector.value[startPos - 1])) {
        cursorPosition = startPos - 1
        this.toggleClearSubmit()
      } else {
        this.listTitleContainer.setAttribute('hidden', '')
        this.dateContainer.innerHTML = ''
        this.selectConfirmationButton.setAttribute('hidden', '')
        this.labelInput.classList.add('has-content')
        this.toggleClearSubmit()
      }
      return v.replace(/[^0-9\/]/g, '')
    })
    dateInputSelector.value = values.join('')
    this.inputField.focus()
    if (cursorPosition === -1) cursorPosition++
    dateInputSelector.setSelectionRange(cursorPosition, cursorPosition)
  }

  onKeydown(event) {
    if (this.CalendarInstance.isOpen && (event.keyCode == 37 || event.keyCode == 38 || event.keyCode == 39 || event.keyCode == 40 || event.keyCode == 13)) {
      event.stopPropagation()
    }





    let backCTA =  select('.js-step-navigation')
    let header = select('.js-header')
    let dateField = select('.js-date-container')
    if(backCTA && header && dateField) {
    let positionAt = backCTA.offsetHeight + header.offsetHeight;
      if (!isInViewPort(this.inputField, -positionAt) && isMobileScreen() && (this.inputField === document.activeElement)) {
        let datePosition = dateField.offsetTop
        window.scrollTo(0, datePosition)
      }
    }

  }

  isValidDate(year, month, day) {
    const date = new Date(year, month, day)
    // Handle format error
    if (isNaN(date)) return null

    const currentDate = new Date()
    if (date.getFullYear() === year && date.getMonth() === month && date.getDate() === day && date.setHours(0, 0, 0, 0)) {
      if (date.setHours(0, 0, 0, 0) < currentDate.setHours(0, 0, 0, 0)) {
        return 'past'
      } else {
        return true
      }
    } else {
      return false
    }
  }

  createErrorMessage(domElement, message) {
    this.cleanErrors(this.selectDateForm)
    this.dateContainer.setAttribute('hidden', '')
    this.selectConfirmationButton.setAttribute('hidden', '')
    this.selectConfirmationPara.setAttribute('hidden', '')
    const elem = document.createElement('p')
    const errorMsg = document.createTextNode(message)
    domElement.classList.add('has-error')
    elem.classList.add('error')
    elem.setAttribute('id', 'error-date')
    elem.dataset.errorMessage = 'true'
    elem.appendChild(errorMsg)
    elem.setAttribute('data-test', 'data-test="lblDateErrormsg"')
    const parent = domElement.parentNode
    parent.appendChild(elem)
    this.inputField.setAttribute('aria-describedby', 'error-date')
    this.inputField.setAttribute('aria-invalid', true)
    this.inputField.removeAttribute('tabindex')
    focusElement(this.inputField)
    return elem
  }

  cleanErrors(element) {
    this.dateContainer.removeAttribute('hidden')
    const hasErrorClasses = getCollection('.has-error', element)
    const errorMessages = getCollection('.error', element)
    for (let i = 0; i < hasErrorClasses.length; i++) {
      hasErrorClasses[i].classList.remove('has-error')
    }
    for (let i = 0; i < errorMessages.length; i++) {
      errorMessages[i].parentNode.removeChild(errorMessages[i])
    }
    this.inputField.removeAttribute('aria-describedby')
    this.inputField.removeAttribute('aria-invalid')
    this.selectConfirmationPara.removeAttribute('hidden')
  }

  destroy = () => {
    this.events.forEach(evt => {
      evt.destroy()
    })
  }

  focusAndSelectToggle(evt) {
    const list = getCollection(DATE_SELECTION_RADIO)
    if (list) {
      list.forEach(element => {
        if (element === evt.delegateTarget) {
          element.parentElement.classList.toggle('is-focused', true)
          element.checked = true
          this.selectedDateSlot = {
            slotIndex: list.indexOf(evt.delegateTarget),
            date: this.availableSlots[list.indexOf(evt.delegateTarget)]
          }
          return true
        } else {
          element.parentElement.classList.toggle('is-focused', false)
          element.checked = false
          return false
        }
      })
      return true
    }
    return false
  }

  focusOutCallback(evt) {
    evt.delegateTarget.parentElement.classList.toggle('is-focused', false)
  }

  selectDateSlot() {
    if (this.selectedDateSlot) {
      this.master.oabDataObject.selectedDateSlot = this.selectedDateSlot
    } else {
      this.master.oabDataObject.selectedDateSlot = {
        slotIndex: 0,
        date: this.availableSlots[0]
      }
    }
    this.master.analyticsHandler('when-user-click-on-select-slot', {
      eventAction: this.selectedServiceTitle,
      eventLabel: 'select this slot',
    })
    this.master.nextStep('redirection-step')
  }

  getBackStepAnalytics() {
    return {
      eventCategory: 'date selection',
      eventLabel: 'back',
      eventAction: this.selectedServiceTitle
    }
  }
}

export default OabDateSelection
